<template>
  <div class="card account-summary">

    <modal v-if="showModal" @close="showModal = false" ref="renameModal">
      <h3 slot="header">Rename</h3>
      <div slot="body">
        <div class="input full-width">
          <input :value="name" type="text" ref="name" />
        </div>
      </div>
      <div slot="footer">
        &nbsp;
        <button class="modal-default-button main-btn primary" @click="rename">
          Rename
        </button>
      </div>
    </modal>

    <AccountSelectWide
      :account="account"
      :on-change="changeAccount"
    />

    <div class="line"></div>
    <div class="columns">
      <div class="column text-body2">
        IBAN: {{ idFormat(account.accountIdentifier) }}
        <textarea ref="iban" class="clipboardArea">{{ idFormat(account.accountIdentifier) }}</textarea>
        <a href="#" @click="copyIdToClipboard">
          <svgicon
            v-tooltip.right="'Copy to clipboard'"
            name="copy"
          ></svgicon>
        </a>
        <br>
        Available balance: {{ account.availableBalance }}<br>
        Account type: {{ account.accountType }}<br>
        Currency: {{ account.currencyId }}<br>
        Reservations made: {{ account.reservations }}<br>
      </div>
      <div class="column text-body2">
        Name: {{ account.name }}
        <a href="#" @click="openRenameModal">
          <svgicon
            v-tooltip.right="'Rename'"
            name="edit"
          ></svgicon>
        </a>
        <br>
        Balance: {{ account.balance }}<br>
        Account holders: {{ account.accountHolders[0].displayName }}<br>
        BIC: {{ account.bic }}<br>
        Date opened: {{ account.accountHolders[0].dateCreated }}<br>
      </div>
    </div>
  </div>
</template>

<script>
  import AccountSelectWide from '@/components/common/AccountSelectWide'
  import Currency from '@/components/mixin/currency'
  import Modal from '@/components/common/Modal.vue'
  import Types from '@/store/types.js'
  import '@/components/icons/copy'
  import '@/components/icons/edit'

  export default {
    name: 'account-select',
    mixins: [Currency],
    props: {
      account: Object,
      onChange: Function
    },
    data () {
      return {
        showModal: false
      }
    },
    components: {
      AccountSelectWide,
      Modal
    },
    computed: {
      name () {
        return this.account.name
      }
    },
    methods: {
      idFormat (id) {
        return id.match(/.{1,4}/g).join(' ')
      },

      changeAccount (id) {
        if (this.onChange) {
          this.onChange(id)
        }
      },
      copyIdToClipboard (e) {
        e.preventDefault()
        const ibanTextarea = this.$refs.iban
        ibanTextarea.select()
        try {
          document.execCommand('copy')
        } catch (err) {
        }
      },
      openRenameModal (e) {
        e.preventDefault()
        this.showModal = true
      },
      rename () {
        this.$refs.renameModal.close()
        this.$store.dispatch(Types.RENAME_ACCOUNT_REQUEST, {
          id: this.account.id,
          name: this.$refs.name.value
        })
      }
    }
  }
</script>

<style lang="scss" scoped>
  .columns .column {
    width: 33%;
  }

  @media screen and (max-width: 1024px) {
    .columns {
      display: block;
    }
    .columns .column {
      width: 100%;
    }
  }

  .account-summary {
    margin-bottom: 20px;

    .text-body2 {
      padding: 8px 12px;
      opacity: .8;
    }
  }

  .clipboardArea {
    width: 1px;
    height: 1px;
    border: 0;
    position: absolute;
    top: 0;
    left: 0;
  }
</style>



