'use strict'

import Api from './api'
import Errors from './errors.json'
import { parseErrors } from '../utils.js'

export default {
  fetchMail ({clientId, pageNumber, pageSize, sort}) {
    return new Promise((resolve, reject) => {
      Api.get('http://private-45bc2-digipay.apiary-mock.com/rest/web-bank/client/clientId/mailbox?pageNumber=pageNumber&pageSize=pageSize&sort=sort')
        .then(response => {
          if (response.data) {
            return resolve(response.data)
          }

          reject(new Error(Errors['NETWORK_ERROR']))
        })
        .catch(err => reject(parseErrors(err)))
    })
  },

  updateOneMail ({id, status}) {
    return new Promise((resolve, reject) => {
      Api.put(`https://private-45bc2-digipay.apiary-mock.com/rest/web-bank/messages/${id}/${status}`)
        .then(response => {
          if (response.data && response.data.data) {
            return resolve()
          }

          reject(new Error(Errors['NETWORK_ERROR']))
        })
        .catch(err => reject(parseErrors(err)))
    })
  }
}




