import Vue from 'vue'
import Vuex from 'vuex'

import auth from '@/store/modules/auth.js'
import mobileMenu from '@/store/modules/mobileMenu.js'
import mobileLayout from '@/store/modules/mobileLayout.js'
import accounts from '@/store/modules/accounts.js'
import transactions from '@/store/modules/transactions.js'
import mailbox from '@/store/modules/mailbox.js'
import additional from '@/store/modules/additional.js'
import products from '@/store/modules/products.js'
import payments from '@/store/modules/payments.js'
import transfers from '@/store/modules/transfers.js'

Vue.use(Vuex)

const store = new Vuex.Store({
  strict: process.env.NODE_ENV !== 'production',

  modules: {
    auth,
    mobileMenu,
    mobileLayout,
    accounts,
    transactions,
    mailbox,
    additional,
    products,
    payments,
    transfers
  }
})

export default store



