<template>
  <div class="column">
    <div class="breadcrumbs">Demo Bank > <a href="#">Payments</a></div>
    <h1>Payments</h1>

    <Tabs
      :options="tabs"
      :selected="selectedSection"
      @change="tabChanged"
    />

    <div class="white-card with-tabs">
      <div class="body">
        <DemoPayment v-if="selectedSection === 'demo'" />
        <TraditionalPayment v-if="selectedSection === 'traditional'" />
      </div>
    </div>
  </div>
</template>

<script>
  import Tabs from '@/components/common/Tabs.vue'
  import DemoPayment from './DemoPayment'
  import TraditionalPayment from './TraditionalPayment'

  import Currency from '@/components/mixin/currency'

  export default {
    name: 'payments',

    mixins: [Currency],

    components: {
      Tabs,
      DemoPayment,
      TraditionalPayment
    },

    computed: {
      selectedSection () {
        return this.$route.params.section || 'demo'
      }
    },

    methods: {
      tabChanged (id) {
        this.$router.push(`/payments/${id}`)
      }
    },

    data () {
      return {
        tabs: [
          {
            id: 'demo',
            title: 'Demo Instant Payment'
          },
          {
            id: 'traditional',
            title: 'Traditional Payment'
          }
        ]
      }
    }
  }

</script>


<style lang="scss" scoped>
  @import "../../styles/bulma/variables.scss";

  .body {
    padding: 24px 32px 32px;
  }

  .breadcrumbs + .text-h1 {
    color: $white-color;
  }

  .white-card.with-tabs {
    border-top-left-radius: 0;
    border-top-right-radius: 0;
  }


  @media (max-width: $desktop) {
    .white-card.with-tabs {
      border-top-left-radius: 6px;
      border-top-right-radius: 6px;
    }
  }


</style>



