<template>
    <div class="content-card mail-card">
        <div class="info-row columns">
            <div class="level is-hidden-tablet is-hidden-desktop">
              <span>Status</span>
              <div class="status read">{{status}}</div>
            </div>
            
            <div class="level">
              <span class="is-hidden-tablet is-hidden-desktop">Date</span>
              <div class="date read">{{date}}</div>
            </div>

            <div class="level">
              <span class="is-hidden-tablet is-hidden-desktop">Sender</span>
              <div class="sender">{{senderDisplayName}}</div>
            </div>
            
            <div class="level">
              <span class="is-hidden-tablet is-hidden-desktop">Subject</span>
              <div class="subject"> 
                  <svgicon name="attachment" class="is-hidden-mobile" width="24px" height="24px" v-if="attachments.length"></svgicon>
                  {{subject}}
                  <svgicon name="attachment" class="is-hidden-desktop is-hidden-tablet" width="24px" height="24px" v-if="attachments.length"></svgicon>
              </div>
            </div>
        </div>

        <div class="columns is-marginless is-hidden-desktop preview" v-if='opened'>
          <div class="message closed column is-paddingless">
            <span>Message:</span>
            {{body}}
          </div>

          <button 
            class="control column is-paddingless close main-btn second"
            @click="opened = false"
            >
            Close
          </button>
        </div>

        <div class="columns is-marginless">
            <div class="message" :class="{closed: !opened, column: !opened}">
                <span>Message:</span>
                <br v-if="opened">
                {{body}}
            </div>

            <button 
              class="control column"
              @click="opened = true"
              v-if="!opened"
              >
              Read More
            </button>

        </div>
        

        <div class="attachments" v-if="this.opened && this.attachments.length">
            <span>Attachments</span>
            <div class="attach"
                v-for="attach in attachments"
                :key="attach.id"
                >
                <svgicon name="attachment" width="24px" height="24px"></svgicon>
                <a href="" class="attach">{{attach.filename}} <span>({{attach.fileSize}}B)</span></a>
            </div>
        </div>

        <div class="controls" v-if="this.opened">
            <button class="main-btn second"@click="changeStatus('UNREAD')">Mark as Unread</button>
            <button class="main-btn second"@click="changeStatus('DELETED')">Delete</button>
        </div>
    </div>
</template>

<script>
import Types from '@/store/types.js'
import { getBaseURL } from '@/utils.js'

export default {
  name: 'mailcard',

  props: {
    model: Object
  },

  methods: {
    changeStatus (status) {
      this.$store.dispatch(Types.MAILBOX_UPDATE_ONE_REQUEST, {
        id: this.model.id,
        status: status
      })
    }
  },

  computed: {
    status () {
      return this.model.status || null
    },

    date () {
      return this.model.dateCreated || null
    },

    senderDisplayName () {
      return this.model.senderDisplayName || ''
    },

    subject () {
      return this.model.subject || ''
    },

    body () {
      return this.model.body || ''
    },

    attachments () {
      const attachments = this.model.attachments ? this.model.attachments.map((i, idx) => {
        const filename = i.filename ? i.filename.split('.').pop() : ''

        if (filename) {
          return Object.assign({}, i, {
            downloadLink: `${getBaseURL}/rest/web-bank/messages/attachments/${idx}.${filename}`
          })
        }
      }) : []

      return attachments
    }
  },

  data () {
    return {
      opened: false
    }
  }
}
</script>



<style lang="scss" scoped>
@import "../../styles/colors.scss";
@import '../../styles/bulma/variables.scss';

.mail-card {
  background: white;
  margin: 8px 0;
  padding: 8px 32px 32px;
  border-radius: 6px;
  color: $text-color;

  &:last-child {
    margin-bottom: 32px;
  }

  .info-row {
    padding: 14px 0;
    border-bottom: 1px solid rgba(0, 0, 0, 0.12);

    .level {
      padding: 0;
      margin: 0;
    }

    .date {
      display: flex;
      align-items: center;

      &.read::before {
        content: "";
        left: 0px;
        width: 6px;
        height: 6px;
        border-radius: 50%;
        margin-right: 18px;
        display: block;
        background: #49e284;
      }
    }


    .sender {
      margin-left: 42px;
    }

    .subject {
      margin-left: 42px;
      display: flex;
      align-items: center;

      .svg-icon {
        color: #bfbfbf;
        margin-right: 6px;
      }
    }
  }

  .columns {
    display: flex;
    align-items: center;
    margin: 0;
  }

  .preview {
    padding: 13px 0;
    border-bottom: 1px solid rgba(0, 0, 0, 0.12);
  }

  .message {
    padding-top: 17px;
    font-size: 16px;
    line-height: 26px;

    span {
      font-size: 14px;
      font-weight: 500;
      line-height: 22px;
    }

    &.closed {
      overflow: hidden;
      white-space: nowrap;
      text-overflow: ellipsis;
    }
  }

  .attachments {
    padding: 25px 0;
    display: flex;
    flex-direction: column;

    & > span {
      font-size: 16px;
      font-weight: 500;
    }

    .attach {
      padding: 5px 0;
      color: $href-color;
      display: flex;
      align-items: center;

      .svg-icon {
        color: #bfbfbf;
        margin-right: 8px;
      }

      span {
        margin-left: 8px;
        font-size: 14px;
        opacity: 0.5;
        line-height: 22px;
        color: $text-color;
      }
    }
  }

  .controls {
    display: flex;

    button:first-child {
      margin-left: auto;
    }

    button {
      padding: 0 25px;
      height: 40px;
      background-color: #dfe4e8;
      border: none;
      outline: none;
      border-radius: 2px;
      cursor: pointer;
      font-size: 15px;
      font-weight: 600;
      line-height: 20px;
      color: #637381;
    }
    button + button {
      margin-left: 16px;
    }
  }

  .control {
    padding: 0;
    height: 30px;
    max-width: 102px;
    background-color: #dfe4e8;
    border: none;
    outline: none;
    border-radius: 2px;
    cursor: pointer;
    font-size: 15px;
    font-weight: 600;
    line-height: 20px;
    color: #637381;

    &.close {
      height: 30px;
      max-width: 72px;
      margin-left: 38px;
    }
  }
}

@media (max-width: $desktop) {
  .mail-card .controls button {
    padding: 10px;
  }
}

@media (max-width: $tablet) {
  .mail-card .info-row {
    flex-direction: column;

    .level {
      width: 100%;
      display: flex;
      align-items: start;

      span + * {
        text-align: right;
        display: block;
      }

      * {
        margin: 0;
      }

      .date {
        &.read::before {
          display: none;
        }
      }

      .status {
        &.read {
          color: #49e284;
        }
      }
    }
  }
}
</style>



