'use strict'

import Types from '@/store/types.js'

const state = {
  enabled: false
}

const actions = {
  [ Types.MOBILE_LAYOUT_ENABLE ] ({ commit }) {
    commit(Types.MOBILE_LAYOUT_ENABLE)
  },
  [ Types.MOBILE_LAYOUT_DISABLE ] ({ commit }) {
    commit(Types.MOBILE_LAYOUT_DISABLE)
  }
}

const mutations = {
  [ Types.MOBILE_LAYOUT_ENABLE ] (state) {
    state.enabled = true
  },
  [ Types.MOBILE_LAYOUT_DISABLE ] (state) {
    state.enabled = false
  }
}

export default {
  state,
  actions,
  mutations
}



