<template>
  <div>
    <h4>Confirmation of Payment Details</h4>

    <div class="text-body1 opacity">Please verify all details before confirming your payment by clicking the "Confirm payment" button. You may change your payment details by clicking the "Back" button and returning to the previous screen.</div>

    <ul>
      <li class="columns text-body1"
        v-for="item in info"
        :key="item.key"
        v-if="item.value">
        <div class="column is-one-third">{{item.key}}</div>
        <div class="column is-two-thirds">
          <span>{{item.value}}</span>
          <span v-if="item.value2">{{item.value2}}</span>
          </div>
      </li>
    </ul>
  </div>
</template>

<script>
import Moment from 'moment'

export default {
  name: 'confirmation-info',

  props: {
    data: Object
  },

  computed: {
    info () {
      let data = [{
        key: 'Source account Name',
        value: this.data.account ? this.data.account.name : null
      },
      {
        key: 'Source account IBAN',
        value: this.data.account ? this.data.account.iban : null
      },
      {
        key: 'Beneficiary name',
        value: this.data.model.name
      },
      {
        key: 'Beneficiary email',
        value: this.data.model.email
      },
      {
        key: 'Transfer message',
        value: this.data.model.message ? this.data.model.message : 'None'
      }]

      if (this.data.model.standingOrderSelected) {
        const typeStanding = this.data.model.standingOrder.condition
        const curCondition = this.data.model.standingOrder[typeStanding]
        const everyType = curCondition ? curCondition.type.value : null
        const stringType = everyType === 'Days' ? 'day' : everyType === 'Weeks' ? 'week' : 'month'
        const description = curCondition ? curCondition.description : null

        data.push({
          key: 'Amount and Frequency',
          value: this.data.model.amount,
          value2: `Every ${stringType}`
        }, {
          key: 'Start date',
          value: Moment(this.data.model.date).format('DD.MM.YYYY')
        }, {
          key: 'Termination Rule',
          value: typeStanding,
          value2: `
            ${description}`
        })

        if (curCondition.endDate) {
          data.push({
            key: 'End date',
            value: Moment(curCondition.endDate).format('DD.MM.YYYY')
          })
        }

        if (curCondition.lastTransfer && curCondition.lastTransfer.selected) {
          data.push({
            key: 'Different Last Transfer',
            value: 'Yes'
          }, {
            key: 'Different Last Transfer Amount',
            value: curCondition.lastTransfer.value
          })
        }
      } else {
        data.push({
          key: 'Transfer amount',
          value: this.data.model.amount
        }, {
          key: 'Due date',
          value: Moment(this.data.model.date).format('DD.MM.YYYY')
        })
      }

      data.push({
        key: 'Bank BIC',
        value: this.data.model.bicInfo ? this.data.model.bicInfo.bic : null
      }, {
        key: 'Bank name',
        value: this.data.model.bicInfo ? this.data.model.bicInfo.institutionName : null
      }, {
        key: 'Bank address',
        value: this.data.model.bicInfo && this.data.model.bicInfo.physicalAddress1 ? `${this.data.model.bicInfo.physicalAddress1}, ${this.data.model.bicInfo.physicalAddress2}, ${this.data.model.bicInfo.physicalAddress3}, ${this.data.model.bicInfo.physicalAddress4}` : null
      }, {
        key: 'Bank city',
        value: this.data.model.bicInfo ? this.data.model.bicInfo.city : null
      }, {
        key: 'Beneficiary address',
        value: this.data.model.address
      }, {
        key: 'Beneficiary city',
        value: this.data.model.city
      }, {
        key: 'Beneficiary postcode',
        value: this.data.model.postcode
      }, {
        key: 'Beneficiary country',
        value: this.data.model.country ? this.data.model.country.title : null
      }, {
        key: 'Priority',
        value: null
      }, {
        key: 'Charge option',
        value: this.data.model.chargingMethod
      }, {
        key: 'Transfer fees',
        value: this.data.model.transferFee
      })

      return data
    }
  }
}
</script>


<style lang="scss" scoped>
  h4 {
    color: #f6a623;
  }

  h4 + .text-body1 {
    padding: 16px 0 32px;
    opacity: .8;
  }

  ul {
    padding: 32px 0 24px;

    li.columns {
      padding: 11px 0;
      margin: 0;
      border-bottom: solid 1px rgba(0, 0, 0, 0.12);

      .column {
        margin: 0;
        padding: 0;

        &:first-child {
          font-weight: 500;
        }

        &.is-two-thirds {
          span {
            font-weight: 500;

            &:last-child {
              font-weight: 300;
            }
          }
        }
      }
    
    }
  }
</style>



