'use strict'

import Types from '@/store/types.js'
import AccountsApi from '@/api/accounts.js'

const state = {
  loading: false,
  renameLoading: false,
  error: null,
  accounts: [],
  rename: null
}

const actions = {
  [ Types.ACCOUNTS_FETCH_REQUEST ] ({commit, rootState, dispatch}) {
    commit(Types.ACCOUNTS_FETCH_REQUEST)
    AccountsApi.fetch(rootState.auth.user.id)
      .then(data => {
        dispatch(Types.ADDITIONAL_SET_MODEL_DATA, {
          type: 'FIXED_TERM',
          data: {
            sourceAccount: data[0],
            settlementAccount: data[0]
          }
        })
        dispatch(Types.TRANSFERS_SET_MODEL_DATA, {
          sourceAccount: data[0],
          targetAccount: data[0]
        })
        commit(Types.ACCOUNTS_FETCH_SUCCESS, data)
      })
      .catch(err => commit(Types.ACCOUNTS_FETCH_FAILURE, err))
  },
  [ Types.RENAME_ACCOUNT_REQUEST ] ({commit, rootState}, data) {
    commit(Types.RENAME_ACCOUNT_REQUEST)
    AccountsApi.rename(rootState.auth.user.id, data.id, data.name)
      .then(result => commit(Types.RENAME_ACCOUNT_SUCCESS, {data, result}))
      .catch(err => commit(Types.RENAME_ACCOUNT_FAILURE, err))
  }
}

const getters = {
  getAccounts: state => filterType => {
    if (!filterType) filterType = 'all'
    const map = {
      all: 'ALL',
      current: 'CURRENT',
      savings: 'SAVINGS',
      term: 'FIXED_TERM',
      load: 'LOAN',
      internal: 'INTERNAL'
    }
    const type = map[filterType]
    if (filterType !== 'all') {
      return state.accounts.filter(v => {
        return v.accountType === type
      })
    } else {
      return state.accounts
    }
  }
}

const mutations = {
  [ Types.ACCOUNTS_FETCH_REQUEST ] (state) {
    state.loading = true
  },

  [ Types.ACCOUNTS_FETCH_SUCCESS ] (state, data) {
    state.accounts = data
    state.error = null
    state.loading = false
  },

  [ Types.ACCOUNTS_FETCH_FAILURE ] (state, err) {
    state.error = err
    state.loading = false
  },

  [ Types.RENAME_ACCOUNT_REQUEST ] (state) {
    state.renameLoading = true
  },

  [ Types.RENAME_ACCOUNT_SUCCESS ] (state, data) {
    state.accounts = state.accounts.map(v => {
      if (v.id === data.data.id) {
        v.name = data.data.name
      }
      return v
    })
    state.error = null
    state.renameLoading = false
  },

  [ Types.RENAME_ACCOUNT_FAILURE ] (state, err) {
    state.error = err
    state.renameLoading = false
  }
}

export default {
  state,
  actions,
  getters,
  mutations
}



