<template>
  <input
    :class="inputClass"
    type="text"
    v-mask="'##.##.####'"
    v-model="value"
    ref="date"
    placeholder="DD.MM.YYYY"
    pattern="(0[1-9]|1[0-9]|2[0-9]|3[01]).(0[1-9]|1[012]).[0-9]{4}"
  />
</template>

<script>
import Pikaday from 'pikaday'

export default {
  name: 'datepicker',

  props: {
    date: String,
    minDate: Date,
    maxDate: Date,
    onChange: Function,
    yearRange: Array,
    inputClass: Object
  },

  mounted () {
    this.picker = new Pikaday({
      firstDay: 1,
      format: 'DD.MM.YYYY',
      field: this.$refs.date,
      keyboardInput: false,
      onSelect: this.onSelect,
      defaultDate: this.date,
      minDate: this.minDate || null,
      maxDate: this.maxDate || null,
      yearRange: this.yearRange
    })

    if (this.date) {
      if (typeof this.date === 'string') {
        this.picker.setDate(new Date(this.date))
      } else {
        this.picker.setDate(this.date)
      }
    }
  },

  methods: {
    onSelect () {
      const value = this.value = this.$refs.date.value
      this.$emit('input', value)
      return this.onChange && this.onChange(value)
    }
  },

  watch: {
    date (val) {
      this.picker.setDate(val, true)
    }
  },

  data () {
    return {
      value: null
    }
  }
}
</script>



