<template>
  <div class="balances" v-if="list.length">
    <h5>Total balance in {{ title }} accounts</h5>
    <table class="table">
      <tbody>
      <tr v-for="v in list">
        <td>{{ v.currency }} accounts ({{ v.count }})</td>
        <td :class="currencyClass(v.balance)">
          {{ formatCurrency(v.balance, v.currency) }}
        </td>
      </tr>
      </tbody>
    </table>
  </div>
</template>

<script>
  import Currency from '@/components/mixin/currency'

  export default {
    name: 'accounts-balances',
    mixins: [Currency],
    props: ['currentType', 'titles', 'accounts'],
    computed: {
      title () {
        return this.currentType
      },
      list () {
        const types = this.accounts
        const counts = {}
        const balances = {}
        for (let v of types) {
          if (!balances[v.currency]) {
            balances[v.currency] = 0
            counts[v.currency] = 0
          }
          balances[v.currency] += v.balance
          counts[v.currency]++
        }
        return Object.keys(counts).map((currency) => {
          return {
            balance: balances[currency],
            count: counts[currency],
            currency
          }
        })
      }
    }
  }
</script>

<style lang="scss" scoped>
  .balances {
    .table td:first-child {
      width: 50%;
    }
    .table td:last-child {
      text-align: right;
    }
    h5 {
      padding: 15px 0;
    }
  }
</style>



