'use strict'

import Types from '@/store/types.js'
import TransfersApi from '@/api/transfers.js'
import StandingOrdersApi from '@/api/standingOrders.js'

const state = {
  data: {},
  loading: false,
  error: null,
  meta: {}
}

const actions = {
  [ Types.CREATE_OUTGOING_TRANSFER_REQUEST ] ({ commit, dispatch }, params) {
    commit(Types.CREATE_OUTGOING_TRANSFER_REQUEST)

    TransfersApi.createOutgoingTransfer(params)
    .then(data => commit(Types.CREATE_OUTGOING_TRANSFER_SUCCESS, data))
    .catch(err => commit(Types.CREATE_OUTGOING_TRANSFER_FAILURE, err))
  },

  [ Types.CREATE_STANDING_ORDER_REQUEST ] ({ commit, dispatch }, params) {
    commit(Types.CREATE_STANDING_ORDER_REQUEST)

    StandingOrdersApi.createStandingOrder(params)
    .then(data => commit(Types.CREATE_STANDING_ORDER_SUCCESS, data))
    .catch(err => commit(Types.CREATE_STANDING_ORDER_FAILURE, err))
  }
}

const mutations = {
  [ Types.CREATE_OUTGOING_TRANSFER_REQUEST ] (state) {
    state.loading = true
  },

  [ Types.CREATE_OUTGOING_TRANSFER_SUCCESS ] (state, data) {
    state.data = data
    state.error = null
    state.loading = false
  },

  [ Types.CREATE_OUTGOING_TRANSFER_FAILURE ] (state, err) {
    state.error = err
    state.loading = false
  },

  [ Types.CREATE_STANDING_ORDER_REQUEST ] (state) {
    state.loading = true
  },

  [ Types.CREATE_STANDING_ORDER_SUCCESS ] (state, data) {
    state.data = data
    state.error = null
    state.loading = false
  },

  [ Types.CREATE_STANDING_ORDER_FAILURE ] (state, err) {
    state.error = err
    state.loading = false
  }
}

export default {
  state,
  actions,
  mutations
}



