<template>
  <div class="body white-card transactions padded">

    <DatepickerModal
      v-if="showDatepickerModal"
      @close="showDatepickerModal = false"
      :dateFrom="customDateFrom"
      :dateTo="customDateTo"
      :on-change="customDateChanged"
    />

    <modal v-if="showStatementModal" @close="showStatementModal = false" ref="statementModal">
      <h3 slot="header">Statement</h3>
      <div slot="body">
        asd
      </div>
      <div slot="footer">
        &nbsp;
        <button class="modal-default-button" @click="showStatementModal = false">
          Ok
        </button>
      </div>
    </modal>

    <div class="cols">
      <div class="col">
      </div>
      <div class="col">
      </div>
    </div>
    <div class="transactions response">
      <div class="columns">
        <div class="column">
          <multiselect
            selectLabel=""
            selectedLabel=""
            deselectLabel=""
            v-model="range"
            placeholder="Range"
            :searchable="false"
            :options="rangeOptions"
            label="title"
            track-by="name"
          >
          </multiselect>
          <div v-if="isCustomRange" class="custom-date">
            <a href="#" @click="changeCustomDateClick">
              <svgicon name="calendar" width="14px" height="14px"></svgicon>
              {{ customRange }}</a>
          </div>
        </div>
        <div class="column">
          <multiselect
            selectLabel=""
            selectedLabel=""
            deselectLabel=""
            v-model="type"
            placeholder="Type"
            :searchable="false"
            :options="typeOptions"
            label="title"
            track-by="name"
          >
          </multiselect>
        </div>
        <div class="column">
          <button @click="generateStatement" class="main-btn primary">Generate statement</button>
        </div>
      </div>
      <AccountsTable
        :header="header"
        :list="list"
      />
    </div>
  </div>
</template>

<script>
  import Moment from 'moment'
  import AccountsTable from './Table2.vue'
  import DatepickerModal from '@/components/common/DatepickerModal.vue'
  import Modal from '@/components/common/Modal.vue'
  import Types from '@/store/types.js'

  export default {
    name: 'account-transactions',
    props: {
      account: Object
    },
    data () {
      return {
        showDatepickerModal: false,
        showStatementModal: false
      }
    },
    components: {
      AccountsTable,
      DatepickerModal,
      Modal
    },
    computed: {
      isCustomRange () {
        return this.$store.state.transactions.range === 'custom'
      },
      customRange () {
        const v = this.$store.state.transactions.customRange
        return v[0].format('YYYY-MM-DD') + '  ' + v[1].format('YYYY-MM-DD')
      },
      customDateFrom () {
        return this.$store.state.transactions.customRange[0].format('YYYY-MM-DD')
      },
      customDateTo () {
        return this.$store.state.transactions.customRange[1].format('YYYY-MM-DD')
      },
      range: {
        get () {
          return this.rangeOptions.filter(v => {
            return v.name === this.$store.state.transactions.range
          })[0]
        },
        set (v) {
          if (!v) {
            return
          }
          this.$store.dispatch(Types.TRANSACTIONS_SET_RANGE, v.name)
          if (v.name === 'custom') {
            this.showDatePickerModal = true
          }
        }
      },
      rangeOptions () {
        return [
          {
            name: '90days',
            title: 'Last 90 days'
          },
          {
            name: 'custom',
            title: 'Custom date range'
          },
          {
            name: 'currentMonth',
            title: 'Current month'
          },
          {
            name: 'lastMonth',
            title: 'Last month'
          },
          {
            name: 'today',
            title: 'Today'
          },
          {
            name: 'yesterday',
            title: 'Yesterday'
          },
          {
            name: 'thisWeek',
            title: 'This week'
          },
          {
            name: 'fullYear',
            title: 'Full year statement'
          }
        ]
      },
      type: {
        get () {
          return this.typeOptions.filter(v => {
            return v.name === this.$store.state.transactions.type
          })[0]
        },
        set (val) {
          // val.name === 'custoimRange'
          this.$store.dispatch(Types.TRANSACTIONS_SET_TYPE, val.name)
        }
      },
      typeOptions () {
        return [
          {
            name: 'both',
            title: 'Show both debits and credits'
          },
          {
            name: 'credits',
            title: 'Show only credits'
          },
          {
            name: 'debits',
            title: 'Show only debits'
          }
        ]
      },
      header () {
        return [
          {
            name: 'date',
            title: 'Date',
            sortField: 'date'
          },
          {
            name: 'remitterName',
            title: 'Remitter Name'
          },
          {
            name: 'recipientName',
            title: 'Recipient Name'
          }, {
            name: 'id',
            title: 'Reference Number'
          }, {
            name: 'amount',
            title: 'Amount'
          }
        ]
      },
      transactions () {
        return this.$store.getters.getTransactions
      },
      list () {
        return this.transactions.map(v => {
          return Object.assign({}, v, {
            date: Moment(v.valueDate).format('D.MM.Y'),
            recipientName: v.beneficiaryName || 'Demo Bank'
          })
        })
      }
    },
    methods: {
      changeCustomDateClick (e) {
        e.preventDefault()
        this.showDatePickerModal = true
      },
      customDateChanged (v) {
        this.$store.dispatch(Types.TRANSACTIONS_SET_CUSTOM_RANGE, [
          Moment(v[0]),
          Moment(v[1])
        ])
      },
      generateStatement () {
        this.showStatementModal = true
        this.$store.dispatch(Types.ACCOUNTS_GENERATE_STATEMENT)
      }
    }
  }
</script>

<style lang="scss">
  @media screen and (max-width: 1024px) {
    .btn-group {
      display: flex;
      justify-content: center;
    }
  }
  .transactions {
    .amount {
      font-size: 18px;
    }

    .status i {
      display: inline-block;
      margin-left: 10px;
      width: 22px;
      height: 22px;
      background-size: contain;
      margin-bottom: -4px;
    }

    .status.pending i {
      background-image: url(/static/img/pending_transaction.svg);
    }

    .custom-date {
      padding: 7px 15px;
    }

    .columns .column:last-child {
      text-align: right;
    }

    @media screen and (max-width: 1024px) {
      & {
        padding-top: 15px;
      }
      .columns {
        display: block;
      }
      .columns .column {
        width: 100%;
      }
    }
  }
</style>



