export default {
  computed: {
    currency () {
      if (!this.$store.state.additional.models.FIXED_TERM.sourceAccount) {
        return null
      }
      return this.$store.state.additional.models.FIXED_TERM.sourceAccount.currency
    }
  },
  methods: {
    formatMaturity (v) {
      let label
      if (v >= 12) {
        if (v / 12 > 1) {
          label = 'years'
        } else {
          label = 'year'
        }
        v = v / 12
      } else {
        if (v === 1) {
          label = 'month'
        } else {
          label = 'months'
        }
      }
      return v + ' ' + label
    }
  }
}



