'use strict'

import Types from '@/store/types.js'
import FormatCurrency from 'format-currency'
import AdditionalApi from '@/api/additional.js'

const initModelsState = {
  CURRENT: {},
  SAVINGS: {},
  FIXED_TERM: {
    sourceAccount: null,
    settlementAccount: null,
    rollover: true
  }
}

const state = {
  step: {
    CURRENT: 1,
    SAVINGS: 1,
    FIXED_TERM: 1
  },
  models: Object.assign({}, initModelsState),
  loading: false
}

const actions = {
  [ Types.ADDITIONAL_SET_STEP ] ({commit}, params) {
    commit(Types.ADDITIONAL_SET_STEP, params)
  },
  [ Types.ADDITIONAL_SET_MODEL_DATA ] ({commit}, params) {
    commit(Types.ADDITIONAL_SET_MODEL_DATA, params)
  },
  [ Types.ADDITIONAL_CREATE_REQUEST ] ({commit, rootState, getters}, type) {
    commit(Types.ADDITIONAL_CREATE_REQUEST)
    AdditionalApi.create(rootState.auth.user.id, type, getters.getCreateData(type))
      .then(data => {
        commit(Types.ADDITIONAL_CREATE_SUCCESS, {type, data})
      })
      .catch(err => commit(Types.ADDITIONAL_CREATE_FAILURE, {type, err}))
  },
  [ Types.ADDITIONAL_CREATE_FINISH ] ({commit}, type) {
    commit(Types.ADDITIONAL_CREATE_FINISH, type)
  }
}

const getters = {
  getAdditionalRate: (state, getters) => type => {
    if (state.models.CURRENT && state.models[type].productId) {
      const product = getters.getProducts(type).find(p => {
        return p.accountProductId === state.models[type].productId
      })
      return FormatCurrency(product.rate)
    }
    return FormatCurrency(0)
  },
  getAdditionalMaturity: (state, getters) => type => {
    if (state.models.CURRENT && state.models[type].productId) {
      const product = getters.getProducts(type).find(p => {
        return p.accountProductId === state.models[type].productId
      })
      return product.maturity
    }
  },
  getCreateData: (state) => type => {
    let typePrefix
    if (type === 'CURRENT') {
      typePrefix = 'Current'
    } else if (type === 'SAVINGS') {
      typePrefix = 'Savings'
    } else {
      typePrefix = 'FixedTerm'
    }
    const model = state.models[type]
    let data = {
      className: `Create${typePrefix}AccountRequest`,
      accountProductId: model.productId,
      name: model.name,
      withholdingTaxCharged: true // @todo   withholdingTaxCharged
    }
    if (type === 'FIXED_TERM') {
      data = Object.assign(data, {
        rollOverOption: !model.rolloverOff,
        interestAccountId: model.settlementAccount.id,
        maturityAccountId: model.settlementAccount.id,
        depositAmount: 50000,
        fundSourceAccountId: model.sourceAccount.id
      })
    }
    return data
  }
}

const mutations = {
  [ Types.ADDITIONAL_SET_STEP ] (state, params) {
    state.step[params.type] = params.step
  },
  [ Types.ADDITIONAL_SET_CURRENCY ] (state, currency) {
    state.model = Object.assign({}, state.model, {currency})
  },
  [ Types.ADDITIONAL_SET_NAME ] (state, name) {
    state.model = Object.assign({}, state.model, {name})
  },
  [ Types.ADDITIONAL_SET_MODEL_DATA ] (state, params) {
    state.models[params.type] = Object.assign({}, state.models[params.type], params.data)
  },
  [ Types.ADDITIONAL_CREATE_REQUEST ] (state) {
    state.loading = true
  },
  [ Types.ADDITIONAL_CREATE_SUCCESS ] (state, params) {
    state.error = null
    state.step[params.type] = 3
    state.models[params.type].created = true
    state.loading = false
  },
  [ Types.ADDITIONAL_CREATE_FAILURE ] (state, params) {
    state.error = params.err
    state.loading = false
  },
  [ Types.ADDITIONAL_CREATE_FINISH ] (state, type) {
    state.step[type] = 1
    state.models[type] = Object.assign({}, initModelsState[type])
  }
}

export default {
  state,
  actions,
  getters,
  mutations
}



