<template>
  <div>
    <div class="breadcrumbs text-caption">Demo Bank >
      <router-link to="/add-account">Open Additional Accounts</router-link>
    </div>
    <h1>Additional Accounts</h1>
    <Tabs
      :options="tabs"
      :selected="selectedTab"
      @change="tabChanged"/>
    <div class="white-card with-tabs">
      <Preloader v-if="loading"/>
      <template v-if="!loading">
        <div class="body">
          <ul class="steps-nav">
            <li :class="{active: step === 1}">1. Account details</li>
            <li :class="{active: step === 2}">2. Confirmation</li>
            <li :class="{active: step === 3}">3. Confirmed</li>
          </ul>
          <div class="steps-body">
            <slot name="body">
            </slot>
          </div>
        </div>
      </template>
    </div>
  </div>
</template>

<script>
  import Tabs from '@/components/common/Tabs.vue'
  import Preloader from '@/components/common/Preloader.vue'

  export default {
    name: 'additional-wrapper',
    props: ['step'],
    components: {
      Tabs,
      Preloader
    },
    computed: {
      selectedTab () {
        return this.$route.path.replace(/\/add-account\/(.*)/, '$1')
      },
      tabs () {
        return [
          {
            id: 'current',
            title: 'Current Account'
          },
          {
            id: 'savings',
            title: 'Savings Account'
          },
          {
            id: 'term',
            title: 'Term Deposit Account'
          }
        ]
      },
      loading () {
        return this.$store.state.products.loading
      }
    },
    methods: {
      tabChanged (id) {
        this.$router.push(`/add-account/${id}`)
      }
    }
  }
</script>



