<template>
  <table class="table titled">
    <tbody>
    <tr>
      <td>Account name</td>
      <td>{{ model.name }}</td>
    </tr>
    <tr>
      <td>Currency</td>
      <td>{{ currency }}</td>
    </tr>
    <tr>
      <td>Applicable interest rate</td>
      <td>{{ rate }}%</td>
    </tr>
    </tbody>
  </table>
</template>

<script>
  export default {
    name: 'additional-summary',
    props: {
      type: {
        type: String,
        required: true
      }
    },
    computed: {
      model () {
        return this.$store.state.additional.models[this.type]
      },
      currency () {
        const product = this.$store.state.products.products[this.type].find(v => {
          return v.accountProductId === this.model.productId
        })
        return product.currencyId
      },
      rate () {
        return this.$store.getters.getAdditionalRate(this.type)
      }
    }
  }
</script>



