<template>
  <div class="chart white-card">
    <div class="account-info">
      <div class="body">
        <Info
          :account="account"
          :onChange="changeAccount"
        />

        <div v-if="loading" class="white-card body">
          <Preloader/>
        </div>

        <div class="btn-group" v-if="!loading">
          <button @click="setPeriodMode('daily')" class="btn" :class="{active:isActivePeriodMode('daily')}">Daily
          </button>
          <button @click="setPeriodMode('monthly')" class="btn" :class="{active:isActivePeriodMode('monthly')}">Monthly
          </button>
          <button @click="setPeriodMode('yearly')" class="btn" :class="{active:isActivePeriodMode('yearly')}">Yearly
          </button>
        </div>
      </div>
    </div>

    <highcharts
      v-if="!loading"
      :options="chartOptions"
      ref="highcharts"
    ></highcharts>
  </div>
</template>

<script>
  import Moment from 'moment'
  import Info from '@/components/common/Info'
  import Currency from '@/components/mixin/currency'
  import Preloader from '@/components/common/Preloader'
  // import Const from '../const'

  let currency = ''
  const tooltipFormatCurrency = amount => {
    return Currency.methods.formatCurrency(amount, currency)
  }

  export default {
    name: 'accounts-chart',
    props: {
      account: Object
    },
    mixins: [Currency],
    components: {
      Info,
      Preloader
    },
    data () {
      return {
        periodMode: 'daily'
      }
    },
    watch: {
      account (v) {
        currency = v.currency
      },
      stats (v) {
        setTimeout(() => {
          this.initDailyExtremes()
        }, 1)
      },
      periodMode () {
        this.initDailyExtremes()
      }
    },
    computed: {
      loading () {
        return this.$store.state.transactions.loading
      },
      stats () {
        return this.$store.getters.getTransactionStats
      },
      debitsSeriesDailyData () {
        return this.stats.map(v => [
          Moment(v.timeRangeValueStart).add(3, 'hours').utc().valueOf(),
          v.data.debitsAmount
        ])
      },
      creditsSeriesDailyData () {
        return this.stats.map(v => [
          Moment(v.timeRangeValueStart).add(3, 'hours').utc().valueOf(),
          v.data.creditsAmount ? -v.data.creditsAmount : 0
        ])
      },
      xAxisData () {
        if (this.periodMode === 'daily') {
          return this.xAxisDailyData
        } else if (this.periodMode === 'monthly') {
          return this.xAxisMonthlyData
        } else {
          return this.xAxisYearlyData
        }
      },
      xAxisDailyData () {
        return {
          tickInterval: Moment.duration(1, 'day').asMilliseconds()
        }
      },
      xAxisMonthlyData () {
        return {
          tickInterval: Moment.duration(1, 'month').asMilliseconds()
        }
      },
      xAxisYearlyData () {
        return {
          tickInterval: Moment.duration(1, 'year').asMilliseconds()
        }
      },
      groupData () {
        return true
      },
      debitsSeriesData () {
        if (this.groupData || this.periodMode === 'daily') {
          return this.debitsSeriesDailyData
        } else if (this.periodMode === 'monthly') {
          return this.debitsSeriesMonthlyData
        } else {
          return this.debitsSeriesYearlyData
        }
      },
      creditsSeriesData () {
        if (this.groupData || this.periodMode === 'daily') {
          return this.creditsSeriesDailyData
        } else if (this.periodMode === 'monthly') {
          return this.creditsSeriesMonthlyData
        } else {
          return this.creditsSeriesYearlyData
        }
      },
      monthInterpolation () {
        const interpolation = {}
        this.stats.map(v => {
          const month = Moment(v.timeRangeValueStart).month()
          const year = Moment(v.timeRangeValueStart).year()
          const milliseconds = Moment([year, month, 1]).utc().valueOf()
          if (!interpolation[milliseconds]) {
            interpolation[milliseconds] = [0, 0]
          }
          interpolation[milliseconds][0] += v.data.debitsAmount
          interpolation[milliseconds][1] += v.data.creditsAmount
        })
        return interpolation
      },
      yearInterpolation () {
        const interpolation = {}
        this.stats.map(v => {
          const year = Moment(v.timeRangeValueStart).year()
          const milliseconds = Moment([year, 1, 1]).utc().valueOf()
          if (!interpolation[milliseconds]) {
            interpolation[milliseconds] = [0, 0]
          }
          interpolation[milliseconds][0] += v.data.debitsAmount
          interpolation[milliseconds][1] += v.data.creditsAmount
        })
        return interpolation
      },
      debitsSeriesMonthlyData () {
        return Object.keys(this.monthInterpolation).map(key => {
          return [
            key,
            this.monthInterpolation[key][0]
          ]
        })
      },
      creditsSeriesMonthlyData () {
        return Object.keys(this.monthInterpolation).map(key => {
          return [
            key,
            this.monthInterpolation[key][1] ? -this.monthInterpolation[key][1] : 0
          ]
        })
      },
      debitsSeriesYearlyData () {
        return Object.keys(this.yearInterpolation).map(key => {
          return [
            key,
            this.yearInterpolation[key][0]
          ]
        })
      },
      creditsSeriesYearlyData () {
        return Object.keys(this.yearInterpolation).map(key => {
          return [
            key,
            this.yearInterpolation[key][1] ? -this.yearInterpolation[key][1] : 0
          ]
        })
      },
      averageSeriesData () {
        return this.debitsSeriesData.map((v, index) => {
          const debit = v[1]
          const credit = this.creditsSeriesData[index] ? this.creditsSeriesData[index][1] : 0
          return [
            v[0],
            debit + credit
          ]
        })
      },
      yAxisMax () {
        if (!this.debitsSeriesData.length) {
          return
        }
        const max1 = Math.max.apply(null, this.debitsSeriesData.map(v => v[1]))
        const max2 = Math.max.apply(null, this.creditsSeriesData.map(v => v[1]))
        return max1 > max2 ? max1 : max2
      },
      chartSeries () {
        const series = JSON.parse(JSON.stringify(this._chartSeries))
        const dataGrouping = {
          approximation: 'sum',
          enabled: true,
          forced: true
        }
        if (this.periodMode === 'monthly' || this.periodMode === 'yearly') {
          dataGrouping.units = [[
            this.periodMode === 'monthly' ? 'month' : 'year',
            [1]
          ]]
          series[0].dataGrouping = dataGrouping
          series[1].dataGrouping = dataGrouping
          series[2].dataGrouping = dataGrouping
        }
        return series
      },
      _chartSeries () {
        return [
          {
            type: 'column',
            name: 'Money in',
            showInNavigator: true,
            data: this.debitsSeriesData
          }, {
            type: 'column',
            name: 'Money out',
            showInNavigator: true,
            data: this.creditsSeriesData
          }, {
            type: 'line',
            name: 'Total net',
            data: this.averageSeriesData
          }
        ]
      },
      chartOptions () {
        // http://jsfiddle.net/gh/get/library/pure/highcharts/highcharts/tree/master/samples/highcharts/demo/combo-dual-axes/
        return {
          chart: {
            type: 'spline'
          },
          title: {
            text: ''
          },
          colors: [
            '#49e284',
            '#ff9191',
            '#0087ff'
          ],
          navigator: {
            height: 60,
            enabled: true,
            maskFill: 'rgba(0,0,0,0.1)',
            handles: {
              enabled: false
            },
            series: {
              // fillOpacity: 1,
              dataGrouping: {
                enabled: false,
                smoothed: false
              }
            }
          },
          scrollbar: {
            // enabled: true
          },
          rangeSelector: {
            enabled: false,
            inputEnabled: false,
            selected: 0,
            buttons: [{
              type: 'month',
              count: 1,
              text: 'Daily'
            }, {
              type: 'year',
              count: 1,
              text: 'Monthly'
            }, {
              type: 'all',
              count: 1,
              text: 'Yearly'
            }]
          },
          credits: {
            enabled: false
          },
          yAxis: {
            visible: false,
            min: -this.yAxisMax,
            max: this.yAxisMax
          },
          tooltip: {
            shared: true,
            formatter: function () {
              return Moment.utc(this.x).format('DD.MM.YYYY') + '<br>' +
                `<b>${this.points[0].series.name}</b>: ` +
                tooltipFormatCurrency(this.points[0].y) + '<br>' +
                `<b>${this.points[1].series.name}</b>: ` +
                tooltipFormatCurrency(this.points[1].y) + '<br>' +
                `<b>${this.points[2].series.name}</b>: ` +
                tooltipFormatCurrency(this.points[2].y)
            }
          },
          legend: {
            itemMarginTop: 20
          },
          xAxis: Object.assign({
            type: 'datetime',
            gridLineWidth: 1,
            ordinal: false,
            crosshair: true,
            dateTimeLabelFormats: {
              day: '%e %b',
              month: '%b \'%y',
              year: '%Y'
            }
          }, this.xAxisData),
          plotOptions: {
            animation: false,
            column: {
              pointWidth: 20
            },
            series: {
              stacking: 'normal'
            },
            line: {
              lineWidth: 1
            }
          },
          series: this.chartSeries
        }
      }
    },
    methods: {
      changeAccount (id) {
        this.$router.push(`/account/${id}`)
      },
      isActivePeriodMode (periodMode) {
        return this.periodMode === periodMode
      },
      setPeriodMode (periodMode) {
        this.periodMode = periodMode
      },
      initDailyExtremes () {
        if (!this.$refs.highcharts || !this.$refs.highcharts.chart.xAxis) {
          return
        }
        const extremes = this.getExtremes()
        if (!extremes) {
          return
        }
        setTimeout(() => {
          this.setExtremes(extremes)
        }, 1)
      },
      getExtremes () {
        if (!this.debitsSeriesDailyData.length) {
          return null
        }
        let firstTime
        const lastTime = this.debitsSeriesDailyData[this.debitsSeriesDailyData.length - 1][0]
        if (this.periodMode === 'daily') {
          firstTime = Moment.utc(lastTime).subtract(16, 'days').valueOf()
        } else if (this.periodMode === 'monthly') {
          firstTime = Moment.utc(lastTime).subtract(12, 'months').valueOf()
        } else if (this.periodMode === 'yearly') {
          firstTime = Moment.utc(lastTime).subtract(3, 'years').valueOf()
        }
        return [
          firstTime,
          lastTime
        ]
      },
      setExtremes (extremes) {
        this.$refs.highcharts.chart.xAxis[0].setExtremes(
          extremes[0],
          extremes[1]
        )
      }
    },
    mounted () {
      this.initDailyExtremes()
      currency = this.account.currency
    }
  }
</script>

<style land="scss" scoped>
  .chart {
    padding-bottom: 5px;
  }
</style>



