<template>
  <table class="table titled">
    <tbody>
    <tr>
      <td>Account name</td>
      <td>{{ model.name }}</td>
    </tr>
    <tr>
      <td>Currency</td>
      <td>{{ currency }}</td>
    </tr>
    <tr>
      <td>Deposit amount</td>
      <td>{{ formatCurrency(50000, currency) }}</td>
    </tr>
    <tr>
      <td>Maturity</td>
      <td>{{ formatMaturity(maturity) }}</td>
    </tr>
    <tr>
      <td>Rollover</td>
      <td>{{ model.rolloverOff ? 'RolloverNo' : 'RolloverYes' }}</td>
    </tr>
    </tbody>
  </table>
</template>

<script>
  import Currency from '@/components/mixin/currency'
  import Common from './common'

  export default {
    name: 'additional-summary',
    mixins: [Currency, Common],
    props: {
      type: {
        type: String,
        required: true
      }
    },
    computed: {
      model () {
        return this.$store.state.additional.models[this.type]
      },
      maturity () {
        return this.$store.getters.getAdditionalMaturity(this.type)
      }
    }
  }
</script>



