'use strict'

import Api from './api'
import Errors from './errors.json'
import { parseErrors } from '../utils.js'

export default {
  fetch (clientId, type, currencyId) {
    return new Promise((resolve, reject) => {
      Api.get(`web-bank/clients/${clientId}/products/${type}/${currencyId}`)
        .then(response => {
          if (response.data) {
            return resolve(response.data)
          }
          reject(new Error(Errors['NETWORK_ERROR']))
        })
        .catch(err => reject(parseErrors(err)))
    })
  }
}



