<template>
  <div>
    <div class="breadcrumbs text-caption">Demo Bank > <a href="#">Accounts</a></div>
    <h1>Accounts</h1>
    <Tabs
      :options="tabs"
      :selected="selectedType"
      @change="tabChanged"/>
    <div class="white-card with-tabs body padded">
      <div v-if="loading">
        <Preloader/>
      </div>
      <div v-if="!loading">
        <template v-if="isAll">
          <AllAccounts :currentType="selectedType" v-if=""/>
          <Balances
            :accounts="allAccounts"
            :titles="tabs"/>
        </template>
        <template v-if="!isAll">
          <Accounts :currentType="selectedType"/>
          <Balances
            :accounts="currentAccounts"
            :currentType="selectedType"
            :titles="tabs"/>
        </template>
      </div>
    </div>
  </div>
</template>

<script>
  import Types from '@/store/types.js'
  import Tabs from '../../common/Tabs.vue'
  import Accounts from './Accounts.vue'
  import AllAccounts from './AllAccounts.vue'
  import Balances from './Balances.vue'
  import Preloader from '@/components/common/Preloader'

  export default {
    name: 'accounts',
    components: {
      Tabs,
      Accounts,
      AllAccounts,
      Balances,
      Preloader
    },
    computed: {
      isAll () {
        return this.selectedType === 'all'
      },
      loading () {
        return this.$store.state.accounts.loading
      },
      currentAccounts () {
        return this.$store.getters.getAccounts(this.selectedType)
      },
      allAccounts () {
        return this.$store.getters.getAccounts()
      },
      selectedType () {
        return this.$route.params.type || 'current'
      },
      tabs () {
        return [
          {
            id: 'current',
            title: 'Current Accounts'
          },
          {
            id: 'savings',
            title: 'Saving Accounts'
          },
          {
            id: 'term',
            title: 'Term Deposit Accounts'
          },
          {
            id: 'all',
            title: 'All Accounts'
          }
        ]
      }
    },
    methods: {
      tabChanged (id) {
        this.$router.push(`/accounts/${id}`)
      },
      fetch () {
        this.$store.dispatch(Types.ACCOUNTS_FETCH_REQUEST)
      }
    },
    beforeMount () {
      this.fetch()
    }
  }
</script>



