<template>
  <div>
    <div class="breadcrumbs text-caption">Demo Bank >
      <router-link to="/transfers">Internal transfers</router-link>
    </div>
    <h1>Internal Transfers</h1>
    <div class="white-card">
      <Preloader v-if="loading"/>
      <template v-if="!loading">
        <div class="body">
          <ul class="steps-nav">
            <li :class="{active: step === 1}">1. Payment details</li>
            <li :class="{active: step === 2}">2. Confirmation</li>
            <li :class="{active: step === 3}">3. Authorize</li>
            <li :class="{active: step === 4}">4. Confirmed</li>
          </ul>
          <div class="steps-body">
            <slot name="body">
            </slot>
          </div>
        </div>
      </template>
    </div>
  </div>
</template>

<script>
  import Preloader from '@/components/common/Preloader.vue'

  export default {
    name: 'transfers-wrapper',
    props: ['step'],
    components: {
      Preloader
    },
    computed: {
      loading () {
        return false
      }
    }
  }
</script>



