<template>
  <div>
    <div v-if="loading">Loading...</div>
    <div v-if="!loading" class="columns account-select-wide">
      <div class="column">
        <AccountSelect
          :account="account"
          @change="onChange"
        />
      </div>
      <div class="column lrg-balance balance-gay">
        <div class="label text-caption">Balance:</div>
        <h3>
          {{ formatCurrency(account.balance, account.currency) }}
        </h3>
      </div>
      <div class="column lrg-balance">
        <div class="label text-caption">Available balance:</div>
        <h3 :class="currencyClass(account.balance)">
          {{ formatCurrency(account.availableBalance, account.currency) }}
        </h3>
      </div>
    </div>
  </div>
</template>

<script>
  import Currency from '@/components/mixin/currency'
  import AccountSelect from '@/components/common/AccountSelect'

  export default {
    name: 'account-select-wide',
    props: {
      account: Object,
      onChange: Function
    },
    mixins: [Currency],
    components: {
      AccountSelect
    },
    computed: {
      loading () {
        return this.$store.state.accounts.loading
      }
    }
  }
</script>

<style lang="scss" scoped>
  @import '../../styles/colors.scss';

  .account-select-wide {
    margin-bottom: 15px;
    .column {
      flex-grow: 1;
    }
    .column:first-child {
      flex-grow: 2;
    }
    .lrg-balance h3 {
      white-space: nowrap;
      color: $text-color;
      font-weight: normal;
      font-size: 22px;
    }
    .balance-gay h3 {
      opacity: 0.5;
    }

    @media screen and (max-width: 1024px) {
      &.columns {
        display: block;
      }
      &.columns .column {
        width: 100%;
      }
    }
  }
</style>



