import Types from '@/store/types.js'

export default {
  computed: {
    typeTitle () {
      return this.type.toLowerCase()
    },
    step () {
      return this.$store.state.additional.step[this.type]
    },
    model () {
      return this.$store.state.additional.models[this.type]
    },
    valid () {
      const fields = Object.keys(this.fields).map(k => this.fields[k])
      return fields.every(f => f.valid === true)
    }
  },
  methods: {
    storeModelData (data) {
      this.$store.dispatch(Types.ADDITIONAL_SET_MODEL_DATA, {
        type: this.type,
        data
      })
    }
  }
}



