import Types from '@/store/types.js'

export default {
  computed: {
    loading () {
      return this.$store.state.products.loading
    },
    name: {
      get () {
        return this.model.name
      },
      set (name) {
        this.storeModelData({ name })
      }
    }
  },
  methods: {
    tabChanged (id) {
      this.$router.push(`/add-account/${id}`)
    },
    next () {
      return this.$validator.validateAll().then((valid) => {
        if (valid) {
          this.$store.dispatch(Types.ADDITIONAL_SET_STEP, {
            type: this.type,
            step: 2
          })
        }
      })
    },
    fetchProducts () {
      if (!this.$store.state.products.products[this.type].length) {
        this.$store.dispatch(Types.PRODUCTS_FETCH_REQUEST, this.type)
      }
    },
    onAgreeChanged (event) {
      this.$store.dispatch(Types.ADDITIONAL_SET_MODEL_DATA, {
        type: this.type,
        data: {
          agree: !this.model.agree
        }
      })
    }
  },
  beforeMount () {
    this.fetchProducts()
  }
}



