<template>
  <StepsWrapper :step="step">
    <div slot="body">
      <h3>Your {typeTitle} account has been created</h3>
      <p>Your {typeTitle} account has been confirmed. Your savings account will now appear in your account listing</p>

      <AdditionalSummary :type="type" />

      <div class="button-footer">
        <button class="main-btn" @click="print">Print</button>
        <button class="main-btn primary" @click="viewAccounts">View accounts</button>
      </div>
    </div>
  </StepsWrapper>
</template>

<script>
  import Types from '@/store/types.js'
  import StepsWrapper from '../StepsWrapper.vue'
  import Step from '../step'
  import Step3 from '../step3'

  export default {
    name: 'additional-current-step3',
    mixins: [Step, Step3],
    props: {
      type: {
        type: String,
        required: true
      }
    },
    components: {
      StepsWrapper
    },
    computed: {
      type () {
        return 'CURRENT'
      }
    },
    methods: {
      print () {
        window.print()
      },
      viewAccounts () {
        this.$store.dispatch(Types.ADDITIONAL_CREATE_FINISH, this.type)
        this.$router.push(`/accounts`)
      }
    }
  }
</script>



