'use strict'

import Api from './api'
import Errors from './errors.json'
import { parseErrors } from '../utils.js'

export default {
  createStandingOrder ({clientId, data}) {
    return new Promise((resolve, reject) => {
      Api.post(`https://private-45bc2-digipay.apiary-mock.com/rest/web-bank/clients/${clientId}/standing-orders`, data)
        .then(response => {
          if (response.data) {
            return resolve(response.data)
          }
          reject(new Error(Errors['NETWORK_ERROR']))
        })
        .catch(err => reject(parseErrors(err)))
    })
  }
}



