<template>
  <div>
    <template v-if="loading">
      <Preloader />
    </template>

    <template v-else>
      <ul class="steps-nav">
        <li :class="{active: step === 1}">1. Payment details</li>
        <li :class="{active: step === 2}">2. Confirmation</li>
        <li :class="{active: step === 3}">3. Authorize</li>
        <li :class="{active: step === 4}">4. Confirmed</li>
      </ul>



      <template v-if="step === 1">
        <div class="text-body1">Select account to be debited</div>
        <Info :account="account"
          :onChange="changeAccount"/>

        <div class="level standing-order">
          <div class="level-left">
            <input type="checkbox" v-model="model.standingOrderSelected" id="standing"/>
            <label
             @click="model.standingOrderSelected = !model.standingOrderSelected">
              <i class="check"></i>
              Create as Standing Order
            </label>
          </div>
          <div class="level-right text-body1">
            {{ formatCurrency(this.account.availableBalance - this.model.amount, this.account.currency) }} (- {{ formatCurrency(this.model.amount, this.account.currency) }})
          </div>
        </div>


        <div class="limit">
          <!-- TODO: Ask about limits -->
          <div class="text-body1">Limit Available: <span>{{ formatCurrency(100000, 'USD') }}</span></div>
          <div class="text-caption">This transfer limit does not apply for internal transfers.</div>
        </div>

        <div class="text-body1">Is the account number an IBAN?</div>
        <div class="columns iban">
          <div class="column input is-narrow">
            <input
              type="radio"
              id="Yes"
              value="Yes"
              :checked="this.model.haveIBAN"
              @change="changeHaveIBAN(true)"
              />
            <label class="check" for="Yes"/>
            <span class="text-body2" @click="changeHaveIBAN(true)">Yes</span>
          </div>

          <div class="column input is-narrow">
            <input
              type="radio"
              id="No"
              value="No"
              :checked="!this.model.haveIBAN"
              @change="changeHaveIBAN(false)"
              />
            <label class="check" for="No"/>
            <span class="text-body2" @click="changeHaveIBAN(false)">No</span>
          </div>
        </div>



        <div class="columns" v-if="model.haveIBAN === true">
          <div class="column input is-one-third">
            <label for="ben-iban">Beneficiary IBAN</label>
            <input id="ben-iban" name="ben-iban" type="text" v-model="model.iban" v-validate="{required: this.model.haveIBAN}" :class="{ 'error': errors.has('ben-iban') }">
            <span v-show="errors.has('ben-iban')">The field is invalid</span>
          </div>

          <div class="column input is-one-third">
            <label for="ben-name">Beneficiary name</label>
            <input id="ben-name" name="ben-name" type="text" v-model="model.name" v-validate="{required: true}" :class="{ 'error': errors.has('ben-name') }">
            <span v-show="errors.has('ben-name')">The field is invalid</span>
          </div>
        </div>


        <template v-else>
          <div class="columns">
            <div class="column input">
              <label for="ben-acc-number">Beneficiary account number</label>
              <input id="ben-acc-number" name="ben-acc-number" type="text" v-model="model.accNumber" v-validate="{required: true}" :class="{ 'error': errors.has('ben-acc-number') }">
              <span v-show="errors.has('ben-acc-number')">The field is invalid</span>
            </div>

            <div class="column input">
              <label for="ben-name">Beneficiary name</label>
              <input id="ben-name" name="ben-name" type="text" v-model="model.name" v-validate="{required: true}" :class="{ 'error': errors.has('ben-name') }">
              <span v-show="errors.has('ben-name')">The field is invalid</span>
            </div>

            <div class="column input">
              <label for="ben-code">Beneficiary SWIFT / BIC code</label>
              <input id="ben-code" name="ben-code" type="text"v-model="model.swiftBicCode" v-validate="{required: true}" :class="{ 'error': errors.has('ben-code') }">
              <span v-show="errors.has('ben-code')">The field is invalid</span>
            </div>
          </div>





          <ul class="bic-info" v-if="model.bicInfo">
            <div class="text-body1">Details of the Financial Institution</div>

            <li class="columns"
              v-for="(value, key) in model.bicInfo"
              :key="key"
              >
              <span class="column is-one-third">{{key}}</span>
              <span class="column is-two-thirds">{{value}}</span>
            </li>
          </ul>

          <div class="columns">
            <div class="column input">
              <label for="ben-address">Beneficiary address</label>
              <input id="ben-address" name="ben-address" type="text" v-model="model.address" v-validate="{required: true}" :class="{ 'error': errors.has('ben-address') }">
              <span v-show="errors.has('ben-address')">The field is invalid</span>
            </div>

            <div class="column input">
              <label for="ben-city">Beneficiary city</label>
              <input id="ben-city" name="ben-city" type="text" v-model="model.city" v-validate="{required: true}" :class="{ 'error': errors.has('ben-city') }">
              <span v-show="errors.has('ben-city')">The field is invalid</span>
            </div>

            <div class="column input">
              <label for="ben-postcode">Beneficiary postcode</label>
              <input id="ben-postcode" name="ben-postcode" type="text" v-model="model.postcode" v-validate="{required: true}" :class="{ 'error': errors.has('ben-postcode') }">
              <span v-show="errors.has('ben-postcode')">The field is invalid</span>
            </div>
          </div>

          <div class="columns">
            <div class="column is-one-third input">
              <label>Beneficiary country</label>
              <multiselect v-model="model.country"
                deselect-label="Can't remove this value"
                track-by="id"
                label="title"
                placeholder="Select one"
                :options="countries"
                :searchable="false"
                :allow-empty="false">
              </multiselect>
            </div>
          </div>
        </template>

        <div class="traditional-options" >
          <div class="text-body1">Choose charge option</div>

          <div class="option">
            <input
              type="radio"
              id="OUR"
              value="OUR"
              :checked="this.model.chargingMethod === 'OUR'"
              @change="changeChargingMethod('OUR')"
            />
            <label class="check" for="OUR"/>
            <span class="text-body2" @click="changeChargingMethod('OUR')">OUR</span>
            <div class="text-body2">I agree to pay all transfer charges.</div>
          </div>

          <div class="option">
            <input
              type="radio"
              id="BEN"
              value="BEN"
              :checked="this.model.chargingMethod === 'BEN'"
              @change="changeChargingMethod('BEN')"
            />
            <label class="check" for="BEN"/>
            <span class="text-body2" @click="changeChargingMethod('BEN')">BEN</span>
            <div class="text-body2">I will not pay any charges in relation to this transfer, and will therefore only transfer the instructed payment minus any charges incurred. These charges shall be borne by the beneficiary.</div>
          </div>

          <div class="option">
            <input
              type="radio"
              id="SHA"
              value="SHA"
              :checked="this.model.chargingMethod === 'SHA'"
              @change="changeChargingMethod('SHA')"
            />
            <label class="check" for="SHA"/>
            <span class="text-body2" @click="changeChargingMethod('SHA')">SHA</span>
            <div class="text-body2">I agree to pay my banks outgoing transfer charge, while the beneficiary shall incur all other charges.</div>
          </div>
        </div>

        <div class="columns" >
          <div class="column is-one-third input">
            <label for="ben-amount">Transfer amount</label>
            <!-- TODO: Max between Limit and AvailableBalance -->
            <input id="ben-amount" name="ben-amount" type="text" v-model="model.amount" v-validate="{required: true, min: 0}" :class="{ 'error': errors.has('ben-amount') }">
            <span v-show="errors.has('ben-amount')">The field is invalid</span>
          </div>

          <div class="column is-one-third input calendar">
            <label for="ben-date">{{model.standingOrderSelected ? 'Start date' : 'Due date'}}</label>
            <!-- TODO:  -->
            <Datepicker id="ben-date" :on-change="onDateChange" :date="model.date" :minDate="new Date()"/>
            <svgicon name="calendar" width="24px" height="24px"></svgicon>
          </div>
        </div>

        <div class="columns" >
          <div class="column is-two-thirds input">
            <label for="ben-message">Transfer message</label>
            <textarea id="ben-message" placeholder="Type your message here" v-model="model.message"></textarea>
          </div>
        </div>


        <StandingOrder v-if="model.standingOrderSelected"
          :param="model.standingOrder"
          :onChange="standingOrderChange"
          >
        </StandingOrder>

        <div class="controls">
          <button class="main-btn second" v-if="step !== 1" @click="backStep">Next</button>
          <button class="main-btn primary" :disabled="nextDisabled" @click="nextStep">Next</button>
        </div>

      </template>



      <template v-if="step === 2">
        <ConfirmationInfo :data="confirmationData" />

        <div class="controls">
          <button class="main-btn second" v-if="step !== 1" @click="backStep">Back</button>
          <button class="main-btn primary" :disabled="nextDisabled" @click="nextStep">Confirm payment</button>
        </div>
      </template>

            <template v-if="step === 3">
        <h4>Authorise transaction</h4>

        <div class="text-body1">You need to authorise this transaction before it can be confirmed</div>

        <div class="columns">
          <h5 class="column is-narrow">Waiting for push notification</h5>
          <div class="column is-narrow">
            *spinner*
          </div>
        </div>

        <div class="columns">
          <h5 class="column is-narrow text">or</h5>
          <div class="column is-narrow">
            <input id="passcode" name="passcode" type="text" placeholder="Enter passcode" v-model="model.passcode" v-validate="{required: true}" :class="{ 'error': errors.has('passcode') }">
            <span v-show="errors.has('passcode')">The field is invalid</span>
          </div>
          <button class="column is-narrow main-btn second" :disabled="!model.passcode || errors.has('passcode')" @click="authorise">Authorise transaction</button>
          <button class="column is-narrow main-btn second" @click="authoriseLater">Authorise later</button>
        </div>

        <div class="text-body2">
          Click the ModirumID push notification on your mobile or tablet to authenticate your identity and authorise this transaction. If your mobile or tablet is not displaying the ModirumID push notification, open ModirumID manually, generate a "Manual OTP" and input the 8 digit passcode into the space provided above.
        </div>
      </template>

      <template v-if="step === 4">
        <h3 class="final">Your payment transaction has been confirmed</h3>

        <div class="text-body1">
          Your transaction has been saved for you to authorise at a later stage. This transfer will appear in the "Pending Transactions" section of your online banking platform, and will not be processed before it is duly authorised. You may print a confirmation of this transaction instruction by clicking the "Print" button at the bottom of this panel.
        </div>

        <div class="trancastion-details">
          <div class="text-body1">Transaction details</div>

          <ul class="info">
            <li>Reference Number: {{transactionInfo.transaction.id}}</li>
            <li>Booking Date: {{transactionInfo.transaction.bookingDate}}</li>
            <li>Value Date: {{transactionInfo.transaction.valueDate}}</li>
            <li>Currency: {{transactionInfo.transaction.currencyId}}</li>
            <li>Amount: {{transactionInfo.transaction.amount}}</li>
            <li>Status: {{transactionInfo.transaction.status}}</li>
            <li>Message: {{transactionInfo.transaction.messageToBeneficiary}}</li>
          </ul>
        </div>

        <div class="source-details">
          <div class="text-body1">Source details</div>

          <ul class="info">
            <li>Reference Number: 57</li>
            <li>Booking Date: 05.09.17</li>
          </ul>
        </div>

        <div class="ben-details">
          <div class="text-body1">Beneficiary details</div>

          <ul class="info">
            <li>Reference Number: 57</li>
            <li>Booking Date: 05.09.17</li>
          </ul>
        </div>

        <div class="pending">
          <div class="text-body1">Pending authorisation:</div>

          <a class="profile">
            <svgicon name="profile" width="24px" height="24px"></svgicon>
            <span>Aleshia Tomkiewicz</span>
          </a>
        </div>

        <div class="controls">
          <button class="main-btn second" @click="print">Print</button>
          <button class="main-btn primary" @click="newPayment">Make another payment</button>
        </div>
      </template>
    </template>
  </div>
</template>

<script>
  import Moment from 'moment'

  import Preloader from '@/components/common/Preloader'
  import Info from '@/components/common/Info'
  import Datepicker from '@/components/common/Datepicker'
  import StandingOrder from '../common/StandingOrder'
  import Types from '@/store/types.js'
  import ConfirmationInfo from './ConfirmationInfo'

  import Currency from '@/components/mixin/currency'

  const getInitialData = () => {
    return {
      accountId: null,
      haveIBAN: true,
      iban: null,
      name: '',
      accNumber: null,
      swiftBicCode: '',
      address: '',
      city: '',
      postcode: '',
      country: {},
      chargingMethod: 'OUR',
      amount: null,
      date: null,
      message: '',
      standingOrderSelected: false,
      standingOrder: {},
      bicInfo: {},
      transferFee: null
    }
  }

  export default {
    name: 'traditional-payment',

    mixins: [Currency],

    components: {
      Preloader,
      Info,
      Datepicker,
      StandingOrder,
      ConfirmationInfo
    },

    watch: {
      step: function (newVal, oldVal) {
        if (newVal === 3 && oldVal === 2) {
          this.sendPayment()
        }

        if (newVal === 4) {
          this.fetchTransaction()
        }
      },

      'model.swiftBicCode' (val) {
        this.model.bicInfo = {
          bic: 'ABSLRUMMXXX',
          institutionName: 'ABSOLUT BANK',
          physicalAddress1: 'TSVETNOY BOULEVARD',
          physicalAddress2: '18',
          physicalAddress3: '',
          physicalAddress4: '',
          city: 'MOSCOW',
          countryId: 'RU',
          id: 144,
          location: '143390'
        }
      }
    },

    computed: {
      loading () {
        return this.$store.state.accounts.loading
      },

      validFields () {
        const fields = Object.keys(this.fields).map(k => this.fields[k])
        return fields.every(f => f.valid === true)
      },

      account () {
        if (this.model.accountId) {
          return this.$store.getters.getAccounts().find(item => item.id === this.model.accountId)
        }

        const accounts = Object.assign([], this.$store.getters.getAccounts())
        return accounts.pop()
      },

      canBeSended () {
        const standingOrderValid = this.standingOrderSelected ? this.standingOrder.valid : true

        return this.validFields && standingOrderValid
      },

      nextDisabled () {
        if (this.step === 1) {
          return !this.canBeSended
        }

        return false
      },

      confirmationData () {
        return {
          model: this.model,
          account: this.account
        }
      },

      transactionInfo () {
        return this.$store.state.transactions.data
      },

      stateData () {
        return this.state.payments.data
      },

      state () {
        return this.$store.state
      }
    },

    methods: {
      changeAccount (id) {
        this.model.accountId = id
      },

      fetch () {
        this.$store.dispatch(Types.ACCOUNTS_FETCH_REQUEST)
      },

      onDateChange (val) {
        this.model.date = Moment(val, 'DD.MM.YYYY').format()
      },

      standingOrderChange (val) {
        this.model.standingOrder = val
      },

      nextStep () {
        this.step++
      },

      backStep () {
        this.step = this.step > 1 ? this.step - 1 : this.step
      },

      changeChargingMethod (val) {
        this.model.chargingMethod = val
      },

      changeHaveIBAN (val) {
        this.errors.clear()
        this.model.haveIBAN = val
      },

      sendPayment () {
        let paymentData = {
          creditAccount: {},
          debitAccountId: this.account ? this.account.id : null,
          debtorClientId: 1,
          amount: this.model.amount,
          currency: this.account ? this.account.currency : null,
          messageToBeneficiary: this.model.message,
          note: '', // ??
          valueDate: this.model.date,
          interbankChargingMethod: this.model.chargingMethod,
          priority: '' // ??
        }

        if (this.model.haveIBAN) {
          console.log('HAVE IBAN')
          paymentData.creditAccount.iban = this.model.iban
        } else {
          console.log('DONT HAVE IBAN')
          paymentData.creditAccount.iban = null
          paymentData.creditAccount.nonIban = {
            accountNumber: this.model.accNumber,
            bankDetails: {
              id: this.model.bicInfo ? this.model.bicInfo.id : null,
              name: this.model.bicInfo ? this.model.bicInfo.institutionName : null,
              bic: this.model.bicInfo ? this.model.bicInfo.bic : null,
              address: this.model.bicInfo ? `${this.model.bicInfo.physicalAddress1}, ${this.model.bicInfo.physicalAddress2}, ${this.model.bicInfo.physicalAddress3}, ${this.model.bicInfo.physicalAddress4}` : null,
              city: this.model.bicInfo ? this.model.bicInfo.city : null,
              postalCode: this.model.bicInfo ? this.model.bicInfo.location : null, // ??
              country: {
                id: this.model.bicInfo ? this.model.bicInfo.countryId : null,
                name: this.model.bicInfo ? this.model.bicInfo.countryId : null
              }
            }
          }
          paymentData.creditAccount.externalAccountHolderDetails = {
            id: null, // ??
            name: this.model.name,
            address: this.model.address,
            city: this.model.city,
            postalCode: this.model.postcode,
            country: {
              id: this.model.country ? this.model.country.id : null,
              name: this.model.country ? this.model.country.title : null
            }

          }
        }

        if (!this.model.standingOrderSelected) {
          this.$store.dispatch(Types.CREATE_OUTGOING_TRANSFER_REQUEST, {
            clientId: 1,
            data: paymentData
          })
        } else {
          const typeStanding = this.model.standingOrder.condition
          const curCondition = this.model.standingOrder[typeStanding]
          const everyType = curCondition ? curCondition.type.value : null
          const everyValue = curCondition ? +curCondition.every : null
          const frequencyType = everyType === 'Days' ? `EVERY_X_DAYS` : everyType === 'Weeks' ? `EVERY_X_WEEKS` : `EVERY_X_MONTHS`

          let obj = {
            frequency: {
              id: null,
              type: frequencyType,
              value: everyValue,
              startDate: this.model.date,
              endDate: curCondition ? curCondition.endDate : null,
              numberOfIterations: curCondition ? curCondition.numberOfPayments : null
            },
            lastTransferAmount: curCondition && curCondition.lastTransfer.selected ? curCondition.lastTransfer.value : null,
            standingOrderIdToReplace: null,
            standingOrderCapableTransferRequest: paymentData
          }

          this.$store.dispatch(Types.CREATE_STANDING_ORDER_REQUEST, {
            clientId: 1,
            data: obj
          })
        }
      },

      newPayment () {
        Object.assign(this, getInitialData())
      },

      authorise () {
        this.$store.dispatch(Types.CREATE_OUTGOING_TRANSFER_REQUEST, {
          clientId: 1,
          data: this.stateData.transactionId
        })
        this.step++
      },

      authoriseLater () {
        this.step++
      },

      fetchTransaction () {
        if (this.stateData && this.stateData.transactionId) {
          this.$store.dispatch(Types.TRANSACTIONS_FETCH_ONE_REQUEST, {
            clientId: 1,
            transactionId: this.stateData.transactionId
          })
        }
      },

      print () {
        console.log('print')
      }
    },

    data () {
      return {
        model: getInitialData(),
        step: 1,
        countries: [
          {
            id: 1,
            title: 'Russia'
          },
          {
            id: 2,
            title: 'Ukraine'
          },
          {
            id: 3,
            title: 'Belarus'
          }
        ]
      }
    },

    beforeMount () {
      this.fetch()
    }
  }

</script>


<style lang="scss" scoped>
  @import "../../styles/bulma/variables.scss";


  .columns {
    margin: 0;

    & + .columns {
      padding-top: 32px;
    }

    &.align-end {
      align-items: flex-end;
    }

    &.iban {
      padding: 24px 0;

      & + .columns {
        padding-top: 8px;
      }
    }
  }

  .traditional-options {
    padding: 40px 0 32px;

    .option {
      padding-top: 27px;

      .text-body2 {
        padding-top: 5px;
      }

      & + .option {
        padding-top: 19px;
      }
    }
  }

  .column.input {
    position: relative;
    padding: 0;

    & + .input {
      padding-left: 16px;
    }

    input,
    textarea {
      width: 100%;
    }

    textarea {
      min-width: 100%;
      min-height: 40px;
      height: 40px;
    }

    input + .svg-icon {
      position: absolute;
      right: 14px;
      margin-top: -32px
    }
  }

  .condition,
  .option {
    label + .text-body2 {
      font-weight: 500;
    }
  }

  .option {
    .text-body2:last-child {
      opacity: .7;
    }
  }

  .bic-info {
    margin: 32px 0;
    .column,
    .columns {
      padding: 0;
    }

    li.columns {
      padding: 8px 0;
      border-bottom: 1px solid rgba(0,0,0,0.12);

      span:first-child {
        font-weight: 500;
      }
    }
  }

  .level.standing-order {
    border-bottom: solid 1px rgba(0, 0, 0, 0.12);
    padding: 17px 0;
    margin-bottom: 24px;
  }

  .limit {
    margin-bottom: 32px;
  }

  .controls {
    display: flex;
    button:first-child {
      margin-left: auto;
    }

    button + button {
      margin-left: 16px;
    }
  }
</style>



