'use strict'

import Types from '@/store/types.js'
import MailboxApi from '@/api/mailbox.js'

const state = {
  data: {},
  loading: false,
  error: null,
  params: {
    pageNumber: 1,
    pageSize: 10,
    totalPages: 1,
    totalItems: 1
  }
}

const actions = {
  [ Types.MAILBOX_FETCH_REQUEST ] ({ commit, dispatch }, params) {
    commit(Types.MAILBOX_FETCH_REQUEST)

    MailboxApi.fetchMail(params)
    .then(data => commit(Types.MAILBOX_FETCH_SUCCESS, data))
    .catch(err => commit(Types.MAILBOX_FETCH_FAILURE, err))
  },

  [ Types.MAILBOX_UPDATE_PARAMS ] ({commit}, params) {
    commit(Types.MAILBOX_UPDATE_PARAMS, params)
  },

  [ Types.MAILBOX_UPDATE_ONE_REQUEST ] ({commit}, params) {
    commit(Types.MAILBOX_UPDATE_ONE_REQUEST)

    MailboxApi.updateOneMail(params)
    .then(() => commit(Types.MAILBOX_UPDATE_ONE_SUCCESS))
    .catch(err => commit(Types.MAILBOX_UPDATE_ONE_FAILURE, err))
  }
}

const mutations = {
  [ Types.MAILBOX_FETCH_REQUEST ] (state) {
    state.loading = true
  },

  [ Types.MAILBOX_FETCH_SUCCESS ] (state, {data, pageNumber, pageSize, totalPages, totalItems}) {
    state.data = data
    Object.assign(state.params, { pageNumber, pageSize, totalPages, totalItems })
    state.error = null
    state.loading = false
  },

  [ Types.MAILBOX_FETCH_FAILURE ] (state, err) {
    state.error = err
    state.loading = false
  },

  [ Types.MAILBOX_UPDATE_PARAMS ] (state, params) {
    Object.assign(state.params, params)
  },

  [ Types.MAILBOX_UPDATE_ONE_REQUEST ] (state) {
    state.loading = true
  },

  [ Types.MAILBOX_UPDATE_ONE_SUCCESS ] (state) {
    state.error = null
    state.loading = false
  },

  [ Types.MAILBOX_UPDATE_ONE_FAILURE ] (state, err) {
    state.error = err
    state.loading = false
  }
}

export default {
  state,
  actions,
  mutations
}



