<template>
  <div>
    <Step1 v-if="step === 1" :type="type" />
    <Step2 v-if="step === 2" :type="type" />
    <Step3 v-if="step === 3" :type="type" />
  </div>
</template>

<script>
  import Step1 from './Step1.vue'
  import Step2 from './Step2.vue'
  import Step3 from './Step3.vue'

  export default {
    name: 'additional-current',
    props: {
      type: {
        type: String,
        required: true
      }
    },
    components: {
      Step1,
      Step2,
      Step3
    },
    computed: {
      step () {
        return this.$store.state.additional.step[this.type]
      }
    }
  }
</script>



