<template>
  <div>
    <Step1 v-if="step === 1"/>
    <Step2 v-if="step === 2"/>
    <Step3 v-if="step === 3"/>
  </div>
</template>

<script>
  import Step1 from './Step1'
  import Step2 from './Step2'
  import Step3 from './Step3'

  export default {
    name: 'additional-term',
    components: {
      Step1,
      Step2,
      Step3
    },
    computed: {
      step () {
        return this.$store.state.additional.step.FIXED_TERM
      }
    }
  }
</script>



