'use strict'

import Types from '@/store/types.js'

const state = {
  opened: false
}

const actions = {
  [ Types.MOBILE_MENU_OPEN ] ({ commit }) {
    commit(Types.MOBILE_MENU_OPEN)
  },
  [ Types.MOBILE_MENU_CLOSE ] ({ commit }) {
    commit(Types.MOBILE_MENU_CLOSE)
  }
}

const mutations = {
  [ Types.MOBILE_MENU_OPEN ] (state) {
    state.opened = true
  },
  [ Types.MOBILE_MENU_CLOSE ] (state) {
    state.opened = false
  }
}

export default {
  state,
  actions,
  mutations
}



