import Types from '@/store/types.js'

export default {
  computed: {
    loading () {
      return this.$store.state.additional.loading
    }
  },
  methods: {
    back () {
      this.$store.dispatch(Types.ADDITIONAL_SET_STEP, {
        type: this.type,
        step: 1
      })
    },
    create () {
      this.$store.dispatch(Types.ADDITIONAL_CREATE_REQUEST, this.type)
    }
  }
}



