<template>
  <div>
    <Step1 v-if="step === 1"/>
    <Step2 v-if="step === 2"/>
  </div>
</template>

<script>
  import Step1 from './Step1'
  import Step2 from './Step2'

  export default {
    name: 'transfers',
    components: {
      Step1,
      Step2
    },
    computed: {
      step () {
        return this.$store.state.transfers.model.step
      }
    }
  }
</script>



