<template>
  <div class="column is-narrow is-paddingless is-hidden-mobile left-menu" :class="{opened, mobile}">
    <div class="top-message">
      <h5 class="greeting">Good morning, <br>Alesha Tomkiewich</h5>
      <div class="text text-body2">Your last visit was on</div>
      <div class="visit-date text-body2">Fri Aug 25 2017 11:10 GMT+0300</div>
    </div>
    <ul class="nav main-nav">
      <router-link tag="li" to="/accounts" class="text-body1">
        <svgicon name="accounts"></svgicon>
        Accounts
      </router-link>
      <router-link tag="li" to="/payments" class="text-body1">
        <svgicon name="payments"></svgicon>
        Payments
      </router-link>
      <router-link tag="li" to="/e-wallet" class="text-body1">
        <svgicon name="e-wallet"></svgicon>
        Kartiera E-Wallet
      </router-link>
      <router-link tag="li" to="/standing-orders" class="text-body1">
        <svgicon name="calendar"></svgicon>
        Standing Orders
      </router-link>
      <router-link tag="li" to="/pending-transactions" class="text-body1">
        <svgicon name="pending_transaction"></svgicon>
        Pending Transactions
      </router-link>
      <router-link tag="li" to="/internal-transfers" class="text-body1">
        <svgicon name="internal_transfer"></svgicon>
        Internal Transfers
      </router-link>
      <router-link tag="li" to="/add-account" class="text-body1">
        <svgicon name="additional_acc"></svgicon>
        Open Additional Accounts
      </router-link>
      <router-link tag="li" to="/claim-funds" class="text-body1">
        <svgicon name="claim_funds"></svgicon>
        Claim Funds
      </router-link>
      <router-link tag="li" to="/mailbox" class="text-body1">
        <svgicon name="mailbox"></svgicon>
        Mailbox
      </router-link>
    </ul>
  </div>
</template>

<script>
  import '@/components/icons/accounts'
  import '@/components/icons/payments'
  import '@/components/icons/e-wallet'
  import '@/components/icons/calendar'
  import '@/components/icons/pending_transaction'
  import '@/components/icons/internal_transfer'
  import '@/components/icons/additional_acc'
  import '@/components/icons/claim_funds'
  import '@/components/icons/mailbox'

  export default {
    name: 'main-nav',
    props: {
      opened: Boolean,
      mobile: Boolean
    }
  }
</script>


<style lang="scss" scoped>
  @import "../../styles/colors.scss";

  .top-message {
    width: 248px;
    color: white;

    .greeting {
      font-weight: 600;
      padding-bottom: 8px;
    }

    .text,
    .visit-date {
      opacity: .7;
    }

    .visit-date {
      padding-bottom: 32px;
    }

  }
  .main-nav li{
    color: white;
    font-weight: 400;
    opacity: .7;
    padding: 7px 0;
    display: flex;
    align-items: end;

    .svg-icon {
      width: 24px;
      height: 24px;
      margin-right: 14px;
    }

    &.router-link-active {
      color: $active-link;
      opacity: 1;

      .svg-icon {
        color: white;
      }
    }

    &:hover {
      opacity: 1;
      cursor: pointer;
    }
  }
  .column {
    margin-right: 16px;
  }
</style>



