<template>
  <div>
    <template v-if="loading">
      <Preloader />
    </template>

    <template v-else>

      <ul class="steps-nav">
        <li :class="{active: step === 1}">1. Payment details</li>
        <li :class="{active: step === 2}">2. Confirmation</li>
        <li :class="{active: step === 3}">3. Authorize</li>
        <li :class="{active: step === 4}">4. Confirmed</li>
      </ul>



      <template v-if="step === 1">
        <div class="text-body1">Select account to be debited</div>
        <Info :account="account"
          :onChange="changeAccount"/>

        <div class="level standing-order">
          <div class="level-left">
            <input
              type="checkbox"
              v-model="model.standingOrderSelected"
              id="standing" />
            <label
             @click="model.standingOrderSelected = !model.standingOrderSelected">
              <i class="check"></i>
              Create as Standing Order
            </label>
          </div>
          <div class="level-right text-body1">
            {{ formatCurrency(this.account.availableBalance - this.model.amount, this.account.currency) }} (- {{ formatCurrency(this.model.amount, this.account.currency) }})
          </div>
        </div>


        <div class="limit">
          <!-- TODO: Ask about limits -->
          <div class="text-body1">Limit Available: <span>{{ formatCurrency(100000, 'USD') }}</span></div>
          <div class="text-caption">This transfer limit does not apply for internal transfers.</div>
        </div>

        <div class="columns">
          <div class="column input">
            <label for="ben-name">Beneficiary name</label>
            <input id="ben-name" name="ben-name" type="text" v-model="model.name" v-validate="{required: true}" :class="{ 'error': errors.has('ben-name') }">
            <span v-show="errors.has('ben-name')">The field is invalid</span>
          </div>

          <div class="column input">
            <label for="ben-email">Beneficiary email / Demo IBAN</label>
            <input id="ben-email" name="ben-email" type="text" v-model="model.email" v-validate="{required: true, email: true}" :class="{ 'error': errors.has('ben-email') }">
            <span v-show="errors.has('ben-email')">The field is invalid</span>
          </div>

          <div class="column input">
            <label for="ben-amount">Transfer amount</label>
            <!-- TODO: Max between Limit and AvailableBalance -->
            <input id="ben-amount" name="ben-amount" type="text" v-model="model.amount" v-validate="{required: true, min: 0}" :class="{ 'error': errors.has('ben-amount') }">
            <span v-show="errors.has('ben-amount')">The field is invalid</span>
          </div>
        </div>

        <div class="columns">
          <div class="column is-one-third input calendar">
            <label for="ben-date">{{model.standingOrderSelected ? 'Start date' : 'Due date'}}</label>
            <!-- TODO:  -->
            <Datepicker id="ben-date" :on-change="onDateChange" :date="model.date" :minDate="new Date()"/>
            <svgicon name="calendar" width="24px" height="24px"></svgicon>
          </div>

          <div class="column is-two-thirds input">
            <label for="ben-message">Transfer message</label>
            <textarea id="ben-message" placeholder="Type your message here" v-model="model.message"></textarea>
          </div>
        </div>

        <StandingOrder v-if="model.standingOrderSelected"
          :param="model.standingOrder"
          :onChange="standingOrderChange"
          >
        </StandingOrder>

        <div class="controls">
          <button class="main-btn second" v-if="step !== 1" @click="backStep">Next</button>
          <button class="main-btn primary" :disabled="nextDisabled" @click="nextStep">Next</button>
        </div>
      </template>



      <template v-if="step === 2">
        <ConfirmationInfo :data="confirmationData" />

        <div class="controls">
          <button class="main-btn second" v-if="step !== 1" @click="backStep">Back</button>
          <button class="main-btn primary" :disabled="nextDisabled" @click="nextStep">Confirm payment</button>
        </div>
      </template>



      <template v-if="step === 3">
        <h4>Authorise transaction</h4>

        <div class="text-body1">You need to authorise this transaction before it can be confirmed</div>

        <div class="columns">
          <h5 class="column is-narrow">Waiting for push notification</h5>
          <div class="column is-narrow">
            *spinner*
          </div>
        </div>

        <div class="columns">
          <h5 class="column is-narrow text">or</h5>
          <div class="column is-narrow">
            <input id="passcode" name="passcode" type="text" placeholder="Enter passcode" v-model="model.passcode" v-validate="{required: true}" :class="{ 'error': errors.has('passcode') }">
            <span v-show="errors.has('passcode')">The field is invalid</span>
          </div>
          <button class="column is-narrow main-btn second" :disabled="!model.passcode || errors.has('passcode')" @click="authorise">Authorise transaction</button>
          <button class="column is-narrow main-btn second" @click="authoriseLater">Authorise later</button>
        </div>

        <div class="text-body2">
          Click the ModirumID push notification on your mobile or tablet to authenticate your identity and authorise this transaction. If your mobile or tablet is not displaying the ModirumID push notification, open ModirumID manually, generate a "Manual OTP" and input the 8 digit passcode into the space provided above.
        </div>
      </template>

      <template v-if="step === 4">
        <h3 class="final">Your payment transaction has been confirmed</h3>

        <div class="text-body1">
          Your transaction has been saved for you to authorise at a later stage. This transfer will appear in the "Pending Transactions" section of your online banking platform, and will not be processed before it is duly authorised. You may print a confirmation of this transaction instruction by clicking the "Print" button at the bottom of this panel.
        </div>

        <div class="trancastion-details">
          <div class="text-body1">Transaction details</div>

          <ul class="info">
            <li>Reference Number: {{transactionInfo.transaction.id}}</li>
            <li>Booking Date: {{transactionInfo.transaction.bookingDate}}</li>
            <li>Value Date: {{transactionInfo.transaction.valueDate}}</li>
            <li>Currency: {{transactionInfo.transaction.currencyId}}</li>
            <li>Amount: {{transactionInfo.transaction.amount}}</li>
            <li>Status: {{transactionInfo.transaction.status}}</li>
            <li>Message: {{transactionInfo.transaction.messageToBeneficiary}}</li>
          </ul>
        </div>

        <div class="source-details">
          <div class="text-body1">Source details</div>

          <ul class="info">
            <li>Reference Number: 57</li>
            <li>Booking Date: 05.09.17</li>
          </ul>
        </div>

        <div class="ben-details">
          <div class="text-body1">Beneficiary details</div>

          <ul class="info">
            <li>Reference Number: 57</li>
            <li>Booking Date: 05.09.17</li>
          </ul>
        </div>

        <div class="pending">
          <div class="text-body1">Pending authorisation:</div>

          <a class="profile">
            <svgicon name="profile" width="24px" height="24px"></svgicon>
            <span>Aleshia Tomkiewicz</span>
          </a>
        </div>

        <div class="controls">
          <button class="main-btn second" @click="print">Print</button>
          <button class="main-btn primary" @click="newPayment">Make another payment</button>
        </div>
      </template>

    </template>

  </div>
</template>


<script>
  import Moment from 'moment'

  import Preloader from '@/components/common/Preloader'
  import Info from '@/components/common/Info'
  import Datepicker from '@/components/common/Datepicker'
  import StandingOrder from '@/components/common/StandingOrder'
  import Types from '@/store/types.js'
  import ConfirmationInfo from './ConfirmationInfo'

  import Currency from '@/components/mixin/currency'

  const getInitialData = () => {
    return {
      accountId: null,
      name: '',
      email: '',
      standingOrderSelected: false,
      standingOrder: {},
      iban: null,
      amount: null,
      date: null,
      message: '',
      step: 1,
      passcode: ''
    }
  }

  export default {
    name: 'demo-payment',

    mixins: [ Currency ],

    components: {
      Info,
      Datepicker,
      StandingOrder,
      Preloader,
      ConfirmationInfo
    },

    watch: {
      step: function (newVal, oldVal) {
        if (newVal === 3 && oldVal === 2) {
          this.sendPayment()
        }

        if (newVal === 4) {
          this.fetchTransaction()
        }
      }
    },

    computed: {
      loading () {
        return this.$store.state.accounts.loading
      },

      validFields () {
        const fields = Object.keys(this.fields).map(k => this.fields[k])
        return fields.every(f => f.valid === true)
      },

      account () {
        if (this.model.accountId) {
          return this.$store.getters.getAccounts().find(item => item.id === this.model.accountId)
        }

        const accounts = Object.assign([], this.$store.getters.getAccounts())
        return accounts.pop()
      },

      canBeSended () {
        const standingOrderValid = this.standingOrderSelected ? this.standingOrder.valid : true

        return this.validFields && standingOrderValid
      },

      nextDisabled () {
        if (this.step === 1) {
          return !this.canBeSended
        }

        return false
      },

      confirmationData () {
        return {
          model: this.model,
          account: this.account
        }
      },

      transactionInfo () {
        return this.$store.state.transactions.data
      },

      stateData () {
        return this.state.payments.data
      },

      state () {
        return this.$store.state
      }
    },

    methods: {
      changeAccount (id) {
        this.model.accountId = id
      },

      fetch () {
        this.$store.dispatch(Types.ACCOUNTS_FETCH_REQUEST)
      },

      onDateChange (val) {
        this.model.date = Moment(val, 'DD.MM.YYYY').format()
      },

      standingOrderChange (val) {
        this.model.standingOrder = val
      },

      nextStep () {
        this.step++
      },

      backStep () {
        this.step = this.step > 1 ? this.step - 1 : this.step
      },

      sendPayment () {
        let paymentData = {
          creditAccount: {
            iban: null,
            // TODO:      demo?
            fullName: this.model.name, // ??
            identityAlias: this.model.email // ??
          },
          debitAccountId: this.account ? this.account.id : null,
          debtorClientId: 1,
          amount: this.model.amount,
          currency: this.account ? this.account.currency : null,
          messageToBeneficiary: this.model.message,
          note: '', // ??
          valueDate: this.model.date,
          interbankChargingMethod: '', // ??
          priority: '' // ??
        }

        if (!this.model.standingOrderSelected) {
          this.$store.dispatch(Types.CREATE_OUTGOING_TRANSFER_REQUEST, {
            clientId: 1,
            data: paymentData
          })
        } else {
          const typeStanding = this.model.standingOrder.condition
          const curCondition = this.model.standingOrder[typeStanding]
          const everyType = curCondition ? curCondition.type.value : null
          const everyValue = curCondition ? +curCondition.every : null
          const frequencyType = everyType === 'Days' ? `EVERY_X_DAYS` : everyType === 'Weeks' ? `EVERY_X_WEEKS` : `EVERY_X_MONTHS`

          let obj = {
            frequency: {
              id: null,
              type: frequencyType,
              value: everyValue,
              startDate: this.model.date,
              endDate: curCondition ? curCondition.endDate : null,
              numberOfIterations: curCondition ? curCondition.numberOfPayments : null
            },
            lastTransferAmount: curCondition && curCondition.lastTransfer.selected ? curCondition.lastTransfer.value : null,
            standingOrderIdToReplace: null,
            standingOrderCapableTransferRequest: paymentData
          }

          this.$store.dispatch(Types.CREATE_STANDING_ORDER_REQUEST, {
            clientId: 1,
            data: obj
          })
        }
      },

      newPayment () {
        Object.assign(this, getInitialData())
      },

      authorise () {
        this.$store.dispatch(Types.CREATE_OUTGOING_TRANSFER_REQUEST, {
          clientId: 1,
          data: this.stateData.transactionId
        })
        this.step++
      },

      authoriseLater () {
        this.step++
      },

      fetchTransaction () {
        if (this.stateData && this.stateData.transactionId) {
          this.$store.dispatch(Types.TRANSACTIONS_FETCH_ONE_REQUEST, {
            clientId: 1,
            transactionId: this.stateData.transactionId
          })
        }
      },

      print () {
        console.log('print')
      }
    },

    data () {
      return {
        step: 1,
        model: getInitialData()
      }
    },

    beforeMount () {
      this.fetch()
    }
  }

</script>


<style lang="scss" scoped>
  @import "../../styles/bulma/variables.scss";

  .columns {
    margin: 0;

    &.align-end {
      align-items: flex-end;
    }

    & + .columns {
      padding-top: 32px;
    }
  }

  .column.input {
    position: relative;
    padding: 8px 0;

    & + .input {
      padding-left: 16px;
    }

    input,
    textarea {
      width: 100%;
    }

    textarea {
      min-width: 100%;
      min-height: 40px;
      height: 40px;
    }

    input + .svg-icon {
      position: absolute;
      right: 14px;
      margin-top: -32px
    }
  }

  .condition {
    label + .text-body2 {
      font-weight: 500;
    }
  }


  .columns {
    margin: 0;

    &.align-end {
      align-items: flex-end;
    }
  }

  .level.standing-order {
    border-bottom: solid 1px rgba(0, 0, 0, 0.12);
    padding: 17px 0;
    margin-bottom: 24px;
  }

  .limit {
    margin-bottom: 32px;
  }

  .column.input {
    position: relative;
    padding: 8px 0;
  }

  .controls {
    display: flex;
    button:first-child {
      margin-left: auto;
    }

    button + button {
      margin-left: 16px;
    }
  }

  .condition,
  .option {
    label + .text-body2 {
      font-weight: 500;
    }
  }

  .option {
    .text-body2:last-child {
      opacity: .7;
    }
  }

  h4 {
    color: #f6a623;
  }

  h3.final {
    color: #00c51a;
    text-align: center;
    padding: 16px 0 40px;
  }

  .trancastion-details {
    margin-top: 32px;
  }

  .trancastion-details,
  .source-details,
  .ben-details,
  .pending {
    padding: 24px 0;
    border-bottom: solid 1px rgba(0, 0, 0, 0.12);

    .info,
    .profile {
      margin-top: 16px;
    }
  }

  .pending .profile {
    color: #0087ff;
    display: block;

    .svg-icon {
      color: #0087ff;
    }
  }

  @media (max-width: $tablet) {
    .level.input {
      .input + .input {
        margin-left: 0;
      }
    }
  }
</style>



