<template>
  <AccountsList :list="list" />
</template>

<script>
  import Currency from '@/components/mixin/currency'
  import AccountsList from './AccountsList.vue'

  export default {
    name: 'accounts-section',
    mixins: [Currency],
    components: {
      AccountsList
    },
    props: ['currentType'],
    computed: {
      list () {
        return this.$store.getters.getAccounts(this.currentType).map(v => {
          v.owner = v.accountHolders[0].displayName
          return v
        })
      }
    }
  }
</script>



