<template>
  <StepsWrapper :step="2">
    <div slot="body">
      <h3>Confirmation of Payment Details</h3>
      Please verify all transfer details before confirming this transaction by clicking the "Submit transfer" button. You may change the details of your transfer by clicking the "Back" button and returning to the previous screen.

      <Summary />

      <div class="button-footer">
        <button class="main-btn primary" @click="back">Back
        </button>
      </div>
    </div>
  </StepsWrapper>
</template>

<script>
  import Types from '@/store/types.js'
  import StepsWrapper from './StepsWrapper.vue'
  import Summary from './Summary.vue'

  export default {
    name: 'transfers-step2',
    components: {
      StepsWrapper,
      Summary
    },
    methods: {
      back () {
        this.storeModelData({step: 1})
      },
      next () {
        this.storeModelData({step: 3})
      },
      storeModelData (data) {
        this.$store.dispatch(Types.TRANSFERS_SET_MODEL_DATA, data)
      }
    }
  }
</script>



