<template>
  <div>
    <input
      type="checkbox"
      class="ios8-switch"
      :id="name"
      :checked="checked"
      :value="true"
      @change="onInput"
    />
    <label :for="name">{{ label }}</label>
  </div>
</template>

<script>
  export default {
    name: 'switch-checkbox',
    props: {
      name: {
        type: String,
        required: true
      },
      value: {
        type: String,
        required: false
      },
      checked: {
        type: Boolean,
        required: false,
        default: false
      },
      label: {
        type: String,
        required: true
      }
    },
    methods: {
      onInput: function (event) {
        // this.onChange(event.target.checked)
        this.$emit('change', event.target.checked)
      }
    }
  }
</script>

<style lang="scss" scoped>
  @import "../../styles/colors";

  input[type="checkbox"].ios8-switch {
    position: absolute;
    margin: 8px 0 0 16px;
  }
  input[type="checkbox"].ios8-switch + label {
    position: relative;
    padding: 5px 0 0 50px;
    line-height: 2.0em;
    user-select: none;
  }
  input[type="checkbox"].ios8-switch + label:before {
    content: "";
    position: absolute;
    display: block;
    left: 0;
    top: 0;
    width: 40px; /* x*5 */
    height: 24px; /* x*3 */
    border-radius: 16px; /* x*2 */
    background: #ccc;
    border: 1px solid #d9d9d9;
    transition: all 0.3s;
  }
  input[type="checkbox"].ios8-switch + label:after {
    content: "";
    position: absolute;
    display: block;
    left: 0;
    top: 0;
    width: 24px; /* x*3 */
    height: 24px; /* x*3 */
    border-radius: 16px; /* x*2 */
    background: #fff;
    border: 1px solid $light-gray;
    transition: all 0.3s;
  }
  input[type="checkbox"].ios8-switch + label:hover:after {
    box-shadow: 0 0 5px rgba(0,0,0,0.3);
  }
  input[type="checkbox"].ios8-switch:checked + label:after {
    margin-left: 16px;
  }
  input[type="checkbox"].ios8-switch:checked + label:before {
    background: $green;
  }
</style>



