<template>
  <div class="item">
    <label
      v-bind:for="id"
      class="radio"
      :class="{ checked }"
    >
      <input
        type="radio"
        v-bind:name="name"
        v-bind:class="className"
        v-bind:id="id"
        v-bind:checked="checked"
        v-bind:value="value"
        v-bind:required="required"
        v-on:change="updateInput">
      <span v-html="label"></span>
    </label>
  </div>
</template>

<script>
  export default {
    props: {
      name: {
        type: String,
        required: false
      },
      className: {
        type: String,
        required: false
      },
      id: {
        type: String,
        required: false
      },
      value: {
        required: false
      },
      required: {
        type: Boolean,
        required: false,
        default: false
      },
      checked: {
        type: Boolean,
        required: false,
        default: false
      },
      label: {
        type: String,
        required: true
      }
    },
    methods: {
      updateInput: function (event) {
        this.$emit('input', event.target.value)
      }
    }
  }
</script>

<style lang="scss" scoped>
  @import "../../styles/colors";

  label {
    &.radio {

      input[type=radio] {
        display: none
      }

      width: 100%;
      display: inline-block;
      cursor: pointer;
      text-align: center;
      user-select: none;
      background: $very-light-gray;
      padding: 10px 20px;
      font-size: 16px;
      border-radius: 3px;

      &:hover {
        background: $light-gray;
      }

      &.checked {
        background: $green;
        color: $white-color;
      }

    }
  }

</style>



