<template>
  <StepsWrapper :step="step">
    <div slot="body">
      <h3>Your savings account has been created</h3>
      <p>Your savings account has been confirmed. Your savings account will now appear in your account listing</p>
      <AdditionalSummary :type="type" />
      <div class="button-footer">
        <button class="main-btn" @click="print">Print</button>
        <button class="main-btn primary" @click="viewAccounts">View accounts</button>
      </div>
    </div>
  </StepsWrapper>
</template>

<script>
  import StepsWrapper from '../StepsWrapper.vue'
  import Step from '../step'
  import Step3 from '../step3'
  import AdditionalSummary from './AdditionalSummary.vue'

  export default {
    name: 'additional-term-step3',
    mixins: [Step, Step3],
    components: {
      StepsWrapper,
      AdditionalSummary
    },
    computed: {
      type () {
        return 'FIXED_TERM'
      }
    },
    methods: {
      viewAccounts () {
      }
    }
  }
</script>



