'use strict'

import Api from './api'
import Errors from './errors.json'
import { parseErrors } from '../utils.js'

export default {
  create (clientId, type, data) {
    return new Promise((resolve, reject) => {
      Api.post(`web-bank/clients/${clientId}/accounts/${type}`, data)
        .then(response => {
          if (response.data) {
            return resolve(response.data)
          }
          reject(new Error(Errors['NETWORK_ERROR']))
        })
        .catch(err => reject(parseErrors(err)))
    })
  }
}



