import FormatCurrency from 'format-currency'

export default {
  methods: {
    formatCurrency: (amount, currency) => {
      const map = {
        USD: '$',
        AED: ' .',
        EUR: '',
        GBP: ''
      }
      if (currency) {
        currency = (map[currency] || currency) + ' '
      } else {
        currency = ''
      }
      return currency + FormatCurrency(amount)
    },
    currencyClass: amount => {
      return 'currency ' + (amount >= 0 ? 'positive' : 'negative')
    }
  }
}



