<template>
  <table class="table">
    <thead>
    <tr>
      <th v-for="field in header">{{ field.title }}</th>
    </tr>
    </thead>
    <tbody>
    <template v-for="v in list">
      <tr :class="(v.messageToBeneficiary ? 'no-border' : '') + ' row-' + getStatusClass(v.status)">
        <td v-for="field in header">
          <span
            v-if="field.name === 'amount'"
          >
            <span :class="'amount ' + currencyClass(v.amount)">{{ formatCurrency(v.amount, v.currencyId) }}</span>
            <span :class="'status ' + getStatusClass(v.status)">
              <i></i>
              <span class="status-label">{{ getStatusClass(v.status) }}</span>
            </span>
            <!-- <svgicon v-if="v.messageToBeneficiary" name="message"></svgicon> -->
          </span>
          <span v-if="field.name !== 'amount'">{{ v[field.name] }}</span>
          <span v-if="field.name === 'date'" class="status-label-response">Status:</span>
        </td>
      </tr>
      <tr v-if="v.messageToBeneficiary" class="message">
        <td colspan="5">
          <b>Message:</b> {{ v.messageToBeneficiary }}
        </td>
      </tr>
    </template>
    </tbody>
  </table>
</template>

<script>
  import Currency from '@/components/mixin/currency'
  import '@/components/icons/accounts'

  export default {
    name: 'account-table',
    props: ['header', 'list'],
    mixins: [Currency],
    methods: {
      getStatusClass (status) {
        const map = {
          PENDING_AUTHORISATION: 'pending',
          APPROVED_AND_COMPLETE: 'complete',
          PENDING_INVITATION_CONFIRMATION: 'pending',
          COMPLETE: 'complete'
        }
        return map[status]
      }
    }
  }
</script>

<style lang="scss" scoped>
  @import '../../../../styles/colors.scss';

  .table {
    .message {
      padding: 0;
    }
    .message td {
      background: $very-light-gray;
      padding: 10px;
    }
    @media screen and (max-width: 1024px) {
      .row-pending {
        border-left: 2px solid $yellow-header;
      }
    }
  }
</style>



