'use strict'

import Api from './api'
import Errors from './errors.json'
import { parseErrors } from '../utils.js'

export default {
  fetch (clientId, accountId) {
    return new Promise((resolve, reject) => {
      Api.get(`web-bank/clients/${clientId}/accounts/transactions/${accountId}`)
        .then(response => {
          if (response.data && response.data.data) {
            return resolve(response.data.data)
          }
          reject(new Error(Errors['NETWORK_ERROR']))
        })
        .catch(err => reject(parseErrors(err)))
    })
  },
  generateStatement (data) {
    return new Promise((resolve, reject) => {
      Api.post(`web-bank/accounts/statements/generate`, data)
        .then(response => {
          if (response.data) {
            return resolve(response.data)
          }
          reject(new Error(Errors['NETWORK_ERROR']))
        })
        .catch(err => reject(parseErrors(err)))
    })
  },
  fetchOne ({clientId, transactionId}) {
    return new Promise((resolve, reject) => {
      Api.get(`https://private-45bc2-digipay.apiary-mock.com/rest/web-bank/clients/${clientId}/transfers/${transactionId}`)
        .then(response => {
          if (response.data) {
            return resolve(response.data)
          }
          reject(new Error(Errors['NETWORK_ERROR']))
        })
        .catch(err => reject(parseErrors(err)))
    })
  },

  fetchPendingTransactions ({clientId, pageNumber, pageSize, sort}) {
    return new Promise((resolve, reject) => {
      Api.get(`https://private-45bc2-digipay.apiary-mock.com/rest/web-bank/clients/${clientId}/transfers/p/pendingAuthorisation?pageNumber=${pageNumber}&pageSize=${pageSize}&sort=${sort}`)
        .then(response => {
          if (response.data) {
            return resolve(response.data)
          }
          reject(new Error(Errors['NETWORK_ERROR']))
        })
        .catch(err => reject(parseErrors(err)))
    })
  }
}




