<template>
  <div class="column">
    <div class="breadcrumbs text-caption"><a href="#">Demo Bank</a> > <a href="#">Mailbox</a></div>
    <h1>Mailbox</h1>
    <div class="sorting-row text-body2">
      <span class="date" @click="toggleOrder" :class="sortOrder">
        Date
      </span>
      <span class="sender">Sender</span>
      <span class="subject">Subject</span>
    </div>

    <template v-if="loading">
      <Preloader/>
    </template>

    <template v-else>
      <mail-card
        v-for="mail in mailbox"
        :key="mail.id"
        :model="mail">
      </mail-card>

      <paginator
        :params="params"
        :on-page-change="onPageChange"
        class="paginator"
        v-if="params"
      />
    </template>
  </div>
</template>

<script>
import MailCard from '@/components/mailbox/MailCard.vue'
import Paginator from '@/components/common/Paginator.vue'
import Preloader from '@/components/common/Preloader'
import Types from '@/store/types.js'

export default {
  name: 'mailbox',

  components: {
    MailCard,
    Paginator,
    Preloader
  },

  methods: {
    fetch () {
      this.$store.dispatch(Types.MAILBOX_FETCH_REQUEST, {
        candidateId: 1,
        pageNumber: this.pageNumber,
        pageSize: this.pageSize,
        sort: this.sort
      })
    },

    toggleOrder () {
      this.sortOrder = this.sortOrder === 'asc' ? 'desc' : 'asc'

      this.$store.dispatch(Types.MAILBOX_UPDATE_PARAMS, {
        sort: this.sortOrder
      })

      this.fetch()
    },

    onPageChange (page) {
      this.$store.dispatch(Types.MAILBOX_UPDATE_PARAMS, {
        pageNumber: page
      })

      this.fetch()
    }
  },

  computed: {
    pageNumber () {
      return this.params ? this.params.pageNumber : null
    },

    pageSize () {
      return this.params ? this.params.pageSize : null
    },

    params () {
      return this.state.params
    },

    loading () {
      return this.state.loading
    },

    date () {
      return this.params ? this.params.sort : null
    },

    mailbox () {
      return this.state.data || []
    },

    state () {
      return this.$store.state.mailbox
    }
  },

  beforeMount () {
    this.fetch()
  },

  data () {
    return {
      sortOrder: 'asc'
    }
  }
}
</script>



<style lang="scss" scoped>
  @import '../../styles/colors.scss';
  @import '../../styles/bulma/variables.scss';

  .sorting-row {
    padding: 16px 0 ;
    color: white;
    opacity: .7;
    font-weight: 300;
    display: flex;

    & > * {
      cursor: default;
    }

    .date {
      display: flex;
      align-items: center;
      cursor: pointer;

      &.asc:after,
      &.desc:after {
        content: '';
        margin-left: 5px;
        display: block;
        width: 0; 
        height: 0; 
        opacity: .4;
      }

      &.asc:after {
        border-left: 6px solid transparent;
        border-right: 6px solid transparent;
        border-top: 8px solid white;
      }
      
      &.desc:after {
        border-left: 6px solid transparent;
        border-right: 6px solid transparent;
        border-bottom: 8px solid white;
      }
    }

    .sender {
      margin-left: 115px;
    }

    .subject {
      margin-left: 78px;
    }
  }

  .breadcrumbs {
    a {
      opacity: 0.7;
      font-weight: 300;

      &:last-child {
        opacity: 1;
      }
    }
  }

  .paginator {
    justify-content: center;
  }

  h1 {
    color: white;
    padding-bottom: 24px;
  }

  @media (max-width: $tablet) {
    .sorting-row {
      display: none;
    }

    h1 {
      padding-bottom: 16px;
    }
  }
</style>


