import Types from '@/store/types.js'

export default {
  methods: {
    fetchAccounts () {
      if (!this.$store.state.accounts.accounts.length) {
        this.$store.dispatch(Types.ACCOUNTS_FETCH_REQUEST)
      }
    }
  },
  beforeMount () {
    this.fetchAccounts()
  }
}



