<template>
  <div class="accounts-list response">
    <table-component
      table-class="table sort"
      :data="list"
      sort-by="iban"
      sort-order="asc"
      :show-filter="false"
      :show-caption="false"
      filter-no-results="No accounts"
    >
      <table-column show="iban" label="IBAN" :formatter="ibanFormatter"></table-column>
      <table-column show="currency" label="Currency"></table-column>
      <table-column show="name" label="Instrument"></table-column>
      <table-column show="owner" label="Account owner"></table-column>
      <table-column show="balance" label="Balance" data-type="numeric" :formatter="balanceFormatter"></table-column>
    </table-component>
  </div>
</template>

<script>
  import Currency from '@/components/mixin/currency'

  export default {
    name: 'accounts-list',
    mixins: [Currency],
    props: ['list'],
    methods: {
      balanceFormatter (balance, row) {
        return '<span class="' + this.currencyClass(balance) + '">' + this.formatCurrency(balance, row.currency) + '</span>'
      },
      ibanFormatter (v, row) {
        return `<a href="/account/${row.id}">${v}</a>`
      }
    }
  }
</script>

<style lang="scss">
  .accounts-list .table {
    th {
      white-space: nowrap;
    }
  }
  @media (max-width: 1024px) {
    .accounts-list .table {
      td:first-child {
        display: block;
      }
      td:nth-child(2) {
        display: none;
      }
      td:nth-child(3) {
        display: block;
        padding: 3px 0 10px 0;
      }
      td:nth-child(4) {
        font-size: 12px;
      }
      td:nth-child(5) {
        float: right;
      }
    }
  }
</style>



