'use strict'

import Errors from './api/errors.json'

export function parseErrors ({ response }) {
  if (!response || !response.data || !response.data.meta || !response.data.meta.error) {
    return Errors['NETWORK_ERROR']
  }

  const error = response.data.meta.error

  if (error.message) {
    return new Error(error.message)
  }

  return Errors['UNHANDLED_ERROR']
}

export function getBaseURL () {
  return 'https://private-89fe56-digipay.apiary-mock.com/'
}



