<template>
  <div class="account-select">
    <multiselect
      label="title"
      track-by="id"
      :value="selectedOption"
      :allow-empty="false"
      :searchable="false"
      :options="options"
      :option-height="104"
      :custom-label="customLabel"
      :show-labels="false"
      @select="selectOption"
    >
      <template slot="option" scope="props">
        <div class="option-desc">
          <span class="m-iban">{{ props.option.iban }}</span>
          <span class="m-title">{{ props.option.title }}</span>
          <span class="m-balance" :class="currencyClass(props.option.balance)">
          {{ formatCurrency(props.option.balance, props.option.currency) }}
        </span>
        </div>
      </template>
    </multiselect>
  </div>
</template>

<script>
  import Currency from '@/components/mixin/currency'

  export default {
    name: 'account-select',
    props: {
      account: {
        type: Object,
        required: true
      }
    },
    mixins: [Currency],
    computed: {
      selectedOption () {
        return Object.assign(this.account, {title: this.account.name})
      },
      options () {
        return this.$store.getters.getAccounts().map(v => {
          v.title = v.name
          return v
        })
      }
    },
    methods: {
      customLabel (v) {
        return `
<span class="m-iban">${v.iban}</span>
<span class="m-title">${v.title}</span>
<span class="m-balance ${this.currencyClass(v.balance)}">${this.formatCurrency(v.balance, v.currency)}</span>
`
      },
      selectOption (option) {
        this.$emit('change', option.id)
      }
    }
  }
</script>

<style lang="scss">
  .account-select {
    .multiselect {
      width: 250px;
    }

    @media screen and (max-width: 1024px) {
      .multiselect {
        width: 100%;
      }
    }

    .multiselect__option,
    .multiselect__single {
      span {
        display: block;
      }
      .m-iban,
      .m-balance {
        font-weight: bold;
      }
    }
  }
</style>



