<template>
  <div>
    <div class="conditions">
      <div class="text-body1">Termination Rule</div>

      <div class="condition">
        <input
          type="radio"
          id="indefiniteRadio"
          value="indefinite"
          :checked="this.standingOrder.condition === 'indefinite'"
          @change="changeCondition('indefinite')"
        />
        <label class="check" for="indefiniteRadio"/>
        <span class="text-body2" @click="changeCondition('indefinite')">Indefinite</span>
        <div class="text-body2">Standing order will not terminate unless specifically requested at a future date</div>
      </div>

      <div class="condition">
        <input
          type="radio"
          id="endDateRadio"
          value="endDate"
          :checked="this.standingOrder.condition === 'endDate'"
          @change="changeCondition('endDate')"
        />
        <label class="check" for="endDateRadio"/>
        <span class="text-body2" @click="changeCondition('endDate')">End date</span>
        <div class="text-body2">Standing order will terminate on a specific date</div>
      </div>

      <div class="condition">
        <input
          type="radio"
          id="numberOfPaymentsRadio"
          value="numberOfPayments"
          :checked="this.standingOrder.condition === 'numberOfPayments'"
          @change="changeCondition('numberOfPayments')"
        />
        <label class="check" for="numberOfPaymentsRadio"/>
        <span class="text-body2" @click="changeCondition('numberOfPayments')">Number of payments</span>
        <div class="text-body2">Standing order will terminate after a determined number of payments</div>
      </div>
    </div>

    <div class="condition">

      <template v-if="this.standingOrder.condition === 'endDate'">
        <div class="columns">
          <div class="column is-one-third input calendar">
            <label for="endDate">End date</label>
            <Datepicker
              v-validate="'required'"
              data-vv-name="endDate"
              id="endDate"
              :on-change="standingOrderEndDateChange"
              :date="currentCondition.endDate"
              :minDate="new Date()"
              :inputClass="{ 'error': errors.has('endDate') }"
            />
            <svgicon name="calendar" width="24px" height="24px"></svgicon>
            <div class="help error" v-show="errors.has('endDate')">The field is invalid</div>
          </div>

          <div class="column is-one-third input check-bottom">
            <div class="input">
              <input
                type="text"
                name="end-date-last-transfer"
                placeholder="Enter a number"
                v-model="currentCondition.lastTransfer.value"
                v-validate="{required: currentCondition.lastTransfer.selected, min: 0, numeric: true}"
                :disabled="!currentCondition.lastTransfer.selected"
                :class="{ 'error': errors.has('end-date-last-transfer') }"
              >
              <span v-show="errors.has('end-date-last-transfer')">The field is invalid</span>
            </div>
            <div class="input lastTransfer">
              <input
                type="checkbox"
                v-model="currentCondition.lastTransfer.selected"
                id="last-value"
              />
              <label
                for="last-value"
                @onChange="this.currentCondition.lastTransfer.selected = !this.currentCondition.lastTransfer.selected">
                <i class="check"></i>
                Different Last Transfer
              </label>
            </div>
          </div>

        </div>
      </template>
      <template v-else-if="this.standingOrder.condition === 'numberOfPayments'">
        <div class="columns">
          <div class="column is-one-third input">
            <label for="numberOfPayments">Number of Payments</label>
            <input
              id="numberOfPayments"
              name="number-of-payments"
              type="text"
              placeholder="Enter a number"
              v-model="currentCondition.numberOfPayments"
              v-validate="{required: true, min: 1, numeric: true}"
              :class="{ 'error': errors.has('number-of-payments') }"
            >
            <span v-show="errors.has('number-of-payments')">The field is invalid</span>
          </div>

          <div class="column is-one-third input check-bottom">
            <div class="input">
              <input
                type="text"
                name="number-of-payments-last-transfer"
                placeholder="Enter a number"
                v-model="currentCondition.lastTransfer2.value"
                v-validate="{required: currentCondition.lastTransfer2.selected, min: 0, numeric: true}"
                :disabled="!currentCondition.lastTransfer2.selected"
                :class="{ 'error': errors.has('number-of-payments-last-transfer') }"
              >
              <span v-show="errors.has('number-of-payments-last-transfer')">The field is invalid</span>
            </div>
            <div class="input lastTransfer">
              <input
                type="checkbox"
                v-model="currentCondition.lastTransfer2.selected"
                id="last-value2"
              />
              <label
                for="last-value2"
                @onChange="this.currentCondition.lastTransfer2.selected = !this.currentCondition.lastTransfer2.selected">
                <i class="check"></i>
                Different Last Transfer
              </label>
            </div>
          </div>

        </div>
      </template>

      <div class="columns align-end every">
        <div class="column is-one-quarter input">
          <label for="indefiniteEvery">Every</label>
          <input
            id="indefiniteEvery"
            :name="`indefinite-every`"
            type="text"
            placeholder="Enter a number"
            v-model="currentCondition.every"
            v-validate="{required: true, min: 1, numeric: true}"
            :class="{ 'error': errors.has(`indefinite-every`) }"
          >
          <span v-show="errors.has(`indefinite-every`)">The field is invalid</span>
        </div>

        <div class="column is-one-quarter input">
          <multiselect
            selectLabel=""
            selectedLabel=""
            deselectLabel=""
            v-model="currentCondition.type"
            deselect-label="Can't remove this value"
            track-by="id"
            label="value"
            placeholder="Select one"
            :options="typeOfStanding"
            :searchable="false"
            :allow-empty="false">
          </multiselect>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
  import Moment from 'moment'
  import Datepicker from '@/components/common/Datepicker'
  import Currency from '@/components/mixin/currency'

  export default {
    name: 'standing-order',

    mixins: [Currency],

    components: {
      Datepicker
    },

    props: {
      param: Object,
      onChange: Function
    },

    watch: {
      standingOrder: {
        handler: function (val) {
          setTimeout(() => {
            const data = Object.assign({}, this.standingOrderObject, {
              valid: this.canBeSended
            })
            this.onChange && this.onChange(data)
          }, 1)
        },
        deep: true
      },

      conditions: {
        handler: function (val) {
          setTimeout(() => {
            const data = Object.assign({}, this.standingOrderObject, {
              valid: this.canBeSended
            })
            this.onChange && this.onChange(data)
          }, 1)
        },
        deep: true
      }
    },

    computed: {
      loading () {
        return this.$store.state.accounts.loading
      },

      currentCondition () {
        return this.conditions[this.standingOrder.condition]
      },

      canBeSended () {
        const fields = Object.keys(this.fields).map(k => {
          if (this.currentCondition.lastTransfer && !this.currentCondition.lastTransfer.selected) {
            if (k === 'end-date-last-transfer') {
              return false
            }
          }
          return this.fields[k]
        }).filter(v => v !== false)
        return fields.every(f => {
          return f.valid === true
        })
      },

      standingOrderObject () {
        return Object.assign({}, this.conditions, this.standingOrder)
      }
    },

    methods: {
      validate () {
        this.$validator.validateAll()
      },

      changeCondition (type) {
        this.standingOrder.condition = type
      },

      standingOrderStartDateChange (val) {
        this.standingOrder.startDate = Moment(val, 'DD.MM.YYYY').format()
      },

      standingOrderEndDateChange (val) {
        this.currentCondition.endDate = Moment(val, 'DD.MM.YYYY').format()
      }
    },

    data () {
      const result = {
        valid: false,
        id: null,
        standingOrder: {
          selected: false,
          condition: 'indefinite'
        },
        price: 1,
        conditions: {
          indefinite: {
            every: null,
            description: 'Standing order will not terminate unless specifically requested at a future date'
          },
          endDate: {
            endDate: null,
            lastTransfer: {
              selected: false,
              value: null
            },
            every: null,
            description: 'Standing order will terminate on a specific date'
          },
          numberOfPayments: {
            numberOfPayments: null,
            lastTransfer2: {
              selected: false,
              value: null
            },
            every: null,
            description: 'Standing order will terminate after a determined number of payments'
          }
        },
        typeOfStanding: [
          {
            value: 'Days',
            id: 1
          },
          {
            value: 'Weeks',
            id: 2
          },
          {
            value: 'Months',
            id: 3
          }
        ]
      }
      Object.keys(result.conditions).map(k => {
        result.conditions[k].type = result.typeOfStanding[0]
      })
      return result
    }
  }

</script>


<style lang="scss" scoped>
  .columns {
    margin: 0;

    &.align-end {
      align-items: flex-end;
    }
  }

  .column.input {
    position: relative;
    padding: 8px;

    input,
    textarea {
      width: 100%;
    }

    textarea {
      min-width: 100%;
      min-height: 40px;
      height: 40px;
    }

    input + .svg-icon {
      position: absolute;
      right: 14px;
      margin-top: -32px
    }
  }

  .lastTransfer {
    padding-top: 8px;
  }

  .column.check-bottom {
    padding-top: 32px;
  }

  .conditions {
    padding: 40px 0 32px;

    .condition {
      padding-top: 25px;

      .text-body2 {
        padding-top: 5px;
      }

      label + .text-body2 {
        font-weight: 500;
        cursor: pointer;
      }

      & + .condition {
        padding-top: 19px;
      }
    }
  }

  .columns.every {
    padding-bottom: 24px;
    align-items: normal;

    .column:last-child {
      padding-top: 32px;
    }
  }
</style>



