<template>
  <StepsWrapper :step="1">
    <div slot="body">
      <div>
        <label>Select source account</label>
        <div class="card">
          <AccountSelectWide
            :account="model.sourceAccount"
            :on-change="onSourceAccountChange"
          />
        </div>
        <label>Select settlement account</label>
        <div class="card">
          <AccountSelectWide
            :account="model.targetAccount"
            :on-change="onTargetAccountChange"
          />
        </div>
      </div>

      <input
        type="checkbox"
        :checked="model.createAsStandingOrder"
        id="standing"
        @change="onStandingOrderFlagChange"
      />
      <label for="standing">
        <i class="check"></i>
        Create as Standing Order
      </label>

      <StandingOrder
        ref="standingOrder"
        v-if="model.createAsStandingOrder"
        :onChange="onStandingOrderChange"
      />

      <hr />

      <p class="input">
        <label for="amount">Amount</label>
        <input type="number" min="1" step="any" id="amount" v-model="amount" v-validate="'required'" />
      </p>

      <p class="input">
        <label for="notes">Notes</label>
        <textarea id="notes" v-model="notes"></textarea>
      </p>

      <!--<hr />-->
      <!--rootValid: {{ rootIsValid }}<br>-->
      <!--standingOrderValid: {{ standingOrderIsValid }}<br>-->
      <!--valid: {{ valid }}-->

      <div class="button-footer">
        <button class="main-btn primary" @click="next" :disabled="!valid">Next
        </button>
      </div>

    </div>
  </StepsWrapper>
</template>

<script>
  import Types from '@/store/types.js'
  import StepsWrapper from './StepsWrapper.vue'
  import AccountSelectWide from '@/components/common/AccountSelectWide.vue'
  import FetchAccounts from '@/components/mixin/fetchAccounts'
  import StandingOrder from '@/components/common/StandingOrder'

  export default {
    name: 'transfers-step1',
    mixins: [FetchAccounts],
    components: {
      StepsWrapper,
      AccountSelectWide,
      StandingOrder
    },
    data () {
      return {
        standingOrderIsValid: false
      }
    },
    computed: {
      model () {
        return this.$store.state.transfers.model
      },
      valid () {
        if (!this.model.createAsStandingOrder) {
          return this.rootIsValid
        }
        return this.rootIsValid && this.standingOrderIsValid
      },
      rootIsValid () {
        const fields = Object.keys(this.fields).map(k => this.fields[k])
        return fields.every(f => f.valid === true)
      },
      amount: {
        get () {
          return this.model.amount
        },
        set (amount) {
          this.storeModelData({ amount })
        }
      },
      notes: {
        get () {
          return this.model.notes
        },
        set (notes) {
          this.storeModelData({ notes })
        }
      }
    },
    methods: {
      validate () {
        this.$refs.standingOrder.validate()
        // this.$validator.validateAll()
      },
      next () {
        if (this.valid) {
          this.storeModelData({step: 2})
        }
      },
      onSourceAccountChange (id) {
        this.storeModelData({
          sourceAccount: this.$store.state.accounts.accounts.find(v => v.id === id)
        })
      },
      onTargetAccountChange (id) {
        this.storeModelData({
          targetAccount: this.$store.state.accounts.accounts.find(v => v.id === id)
        })
      },
      onStandingOrderFlagChange () {
        this.storeModelData({
          createAsStandingOrder: !this.model.createAsStandingOrder
        })
      },
      onStandingOrderChange (v) {
        this.standingOrderIsValid = v.valid
//        this.storeModelData({
//          standingOrder: JSON.parse(JSON.stringify(v))
//        })
      },
      storeModelData (data) {
        this.$store.dispatch(Types.TRANSFERS_SET_MODEL_DATA, data)
      }
    }
  }
</script>



