<template>
  <div id="app"
    class="app-container"
    :class="appStyle"
    >
    <div class="print">Demo Bank</div>
    <header-panel
      :parentToggle="toggleMenu">
    </header-panel>

    <div class="section main-section">
      <div class="container">
        <div class="columns is-marginless">
          <MainNavigation/>
          <router-view
            class="column is-paddingless content"
            >
            </router-view>
        </div>
      </div>

    </div>
    <BottomNavigation/>
  </div>
</template>

<script>
  import HeaderPanel from '@/components/HeaderPanel.vue'
  import MainNavigation from '@/components/navigation/Main.vue'
  import BottomNavigation from '@/components/navigation/Bottom.vue'
  import '@/components/icons'

  export default {
    name: 'app',

    components: {
      MainNavigation,
      BottomNavigation,
      HeaderPanel
    },

    methods: {
      toggleMenu () {
        this.mobileMenuOpened = !this.mobileMenuOpened
      }
    },

    computed: {
      user () {
        return this.$store.state.auth.user
      },

      routeStyle () {
        return this.$route.meta ? this.$route.meta.backgroundClass : 'default'
      },

      appStyle () {
        return {
          mobileMenuOpened: this.mobileMenuOpened,
          [ this.routeStyle ]: true
        }
      }
    },

    data () {
      return {
        mobileMenuOpened: false
      }
    }

  }
</script>

<style lang="scss">
  @import 'styles/index.scss';
  @import 'styles/bulma/variables.scss';

  .app-container {
    display: flex;
    flex-direction: column;
    min-height: 100vh;
    &.mobileMenuOpened {
      overflow: hidden;
      height: 100vh;
    }

    &.accounts {
      background-image: linear-gradient(170deg, #3f0087, #bc54f0);
    }
    &.mailbox {
      background-image: linear-gradient(135deg, #247ba0, #17c29d);
    }
    &.payments {
      background-image: linear-gradient(141deg, #8310a3, #f83f91);
    }
    &.additional {
      background-image: linear-gradient(122deg, #220b55, #a777bf);
    }
    &.transfers {
      background-image: linear-gradient(137deg, #3d3393, #13c58f);
    }
    &.pending {
      background-image: linear-gradient(135deg, #340e6c, #3686c3);
    }
  }

  .main-section {
    padding: 16px 8px;
    flex: 1 0 auto;

    .content {
      overflow: hidden;
    }
  }

  @media (min-width: $desktop) {
    .main-section {
      padding: 40px 8px;
    }

    .app-container.mobileMenuOpened {
      overflow: auto;
      height: auto;
    }
  }
</style>



