'use strict'

import Types from '@/store/types.js'

const initModelState = {
  step: 2,
  sourceAccount: null,
  targetAccount: null
}

const state = {
  model: Object.assign({}, initModelState)
}

const actions = {
  [ Types.TRANSFERS_SET_MODEL_DATA ] ({commit}, params) {
    commit(Types.TRANSFERS_SET_MODEL_DATA, params)
  }
}

const getters = {
}

const mutations = {
  [ Types.TRANSFERS_SET_MODEL_DATA ] (state, data) {
    state.model = Object.assign({}, state.model, data)
  }
}

export default {
  state,
  actions,
  getters,
  mutations
}



