<template>
  <StepsWrapper :step="step">
    <div slot="body">
      <Preloader v-if="loading"/>
      <template v-if="!loading">
        <h3>Confirmation of fixed term account details</h3>
        <p>
          Please verify all savings account details before confirming the opening of your new savings account by clicking the "Create account" button. You may change your account details by clicking the "Back" button and returning to the previous screen.</p>
        <AdditionalSummary :type="type" />
        <div class="button-footer">
          <button class="main-btn" @click="back">Back</button>
          <button class="main-btn primary" @click="create">Create account</button>
        </div>
      </template>
    </div>
  </StepsWrapper>
</template>

<script>
  import StepsWrapper from '../StepsWrapper.vue'
  import Step from '../step'
  import Step2 from '../step2'
  import Preloader from '@/components/common/Preloader.vue'
  import AdditionalSummary from './AdditionalSummary.vue'

  export default {
    name: 'additional-term-step2',
    mixins: [Step, Step2],
    components: {
      StepsWrapper,
      Preloader,
      AdditionalSummary
    },
    computed: {
      type () {
        return 'FIXED_TERM'
      }
    }
  }
</script>



