import Vue from 'vue'
import Router from 'vue-router'
import Accounts from '@/components/accounts/list/Scene'
import Account from '@/components/accounts/item/Scene'
import AdditionalCurrent from '@/components/additional/current/Scene'
import AdditionalSavings from '@/components/additional/savings/Scene'
import AdditionalTerm from '@/components/additional/term/Scene'
import Mailbox from '@/components/mailbox/MailboxScene'
import Payments from '@/components/payments/PaymentsScene'
import Terms from '@/components/pages/Terms'
import Transfers from '@/components/transfers/Scene'
import PendingTransactions from '@/components/pending/PendingTransactionsScene.vue'

import Auth from './auth.js'

Vue.use(Router)

export default new Router({
  mode: 'history',
  routes: [
    {
      path: '/',
      name: 'home',
      redirect: '/accounts'
    },

    {
      beforeEnter: Auth.requireAuth,
      path: '/accounts/:type?',
      name: 'accounts',
      component: Accounts,
      meta: {
        backgroundClass: 'accounts'
      }
    },
    {
      beforeEnter: Auth.requireAuth,
      path: '/account/:id',
      name: 'account',
      component: Account,
      meta: {
        backgroundClass: 'accounts'
      }
    },
    {
      beforeEnter: Auth.requireAuth,
      path: '/add-account',
      redirect: '/add-account/current',
      name: 'additional',
      component: AdditionalCurrent,
      meta: {
        backgroundClass: 'additional'
      }
    },
    {
      path: '/add-account/current',
      name: 'additional-current',
      component: AdditionalCurrent,
      meta: {
        backgroundClass: 'additional'
      }
    },
    {
      beforeEnter: Auth.requireAuth,
      path: '/add-account/savings',
      name: 'additional-savings',
      component: AdditionalSavings,
      meta: {
        backgroundClass: 'additional'
      }
    },
    {
      beforeEnter: Auth.requireAuth,
      path: '/add-account/term',
      name: 'additional-term',
      component: AdditionalTerm,
      meta: {
        backgroundClass: 'additional'
      }
    },
    {
      beforeEnter: Auth.requireAuth,
      path: '/mailbox',
      name: 'mailbox',
      component: Mailbox,
      meta: {
        backgroundClass: 'mailbox'
      }
    },
    {
      beforeEnter: Auth.requireAuth,
      path: '/payments/:section?',
      name: 'payments',
      component: Payments,
      meta: {
        backgroundClass: 'payments'
      }
    },
    {
      path: '/terms',
      name: 'terms',
      component: Terms,
      meta: {
        backgroundClass: 'payments'
      }
    },
    {
      path: '/internal-transfers',
      name: 'transfers',
      component: Transfers,
      meta: {
        backgroundClass: 'transfers'
      }
    },
    {
      beforeEnter: Auth.requireAuth,
      path: '/pending-transactions',
      name: 'pending',
      component: PendingTransactions,
      meta: {
        backgroundClass: 'pending'
      }
    }
  ]
})



