<template>
  <div class="footer container is-hidden-touch">
    <div v-show="opened" class="bottom-bubble-nav-container">
      <ul class="nav bottom-bubble-nav">
        <router-link tag="li" to="/chat" class="text-body2">
          Chat with us!
        </router-link>
        <router-link tag="li" to="/legal" class="text-body2">
          Legal Information
        </router-link>
        <router-link tag="li" to="/charges" class="text-body2">
          Transaction Charges
        </router-link>
        <router-link tag="li" to="/policy" class="text-body2">
          Privacy Policy
        </router-link>
        <router-link tag="li" to="/feedback" class="text-body2">
          Share Feedback
        </router-link>
      </ul>
    </div>
    <ul class="nav bottom-short-nav level">
      <router-link tag="li" to="/get-in-touch" class="text-body2">
        Get In Touch
      </router-link>
      <router-link tag="li" to="/faq" class="text-body2">
        FAQs
      </router-link class="text-body2">
      <li
        v-click-outside="hide"
        @click="open"
        class="more">
        
      </li>
    </ul>
  </div>
</template>

<script>
  import ClickOutside from 'vue-click-outside'

  export default {
    data () {
      return {
        opened: false
      }
    },

    methods: {
      open () {
        this.opened = true
      },

      hide () {
        this.opened = false
      }
    },

    mounted () {
      // prevent click outside event with popupItem.
      // this.popupItem = this.$el
    },

    // do not forget this section
    directives: {
      ClickOutside
    }
  }
</script>


<style lang="scss" scoped>
  .footer {
    min-height: 40px;
    flex-shrink: 0;
    width: 100%;
    position: relative;
  }

  .bottom-short-nav {
    color: #fff;
    justify-content: normal;

    li {
      cursor: pointer;
    }

    li + li {
      padding-left: 16px;
    }
  }

  .bottom-bubble-nav-container {
    position: absolute;
    bottom: 60px;
    width: 205px;

    .bottom-bubble-nav {
      background: #fff;
      padding: 10px;

      .text-body2 {
        font-weight: 300;
      }

      li {
        cursor: pointer;
      }
    }

    &:after {
      position: absolute;
      right: 10px;
      content: "";
      width: 0;
      height: 0;
      border-style: solid;
      border-width: 10px 10px 0;
      border-color: #fff transparent transparent;
    }
  }
</style>



