'use strict'

import Types from '@/store/types.js'
import Moment from 'moment'
import TransactionsApi from '@/api/transactions.js'

const state = {
  loading: false,
  error: null,
  transactions: [],
  range: '90days',
  customRange: [Moment(), Moment()],
  type: 'both',
  statementInProgress: false,
  data: {},
  params: {
    pageNumber: 1,
    pageSize: 10,
    totalPages: 1,
    totalItems: 1
  }
}

const actions = {
  [ Types.TRANSACTIONS_FETCH_REQUEST ] ({commit, rootState}, accountId) {
    commit(Types.TRANSACTIONS_FETCH_REQUEST)
    TransactionsApi.fetch(rootState.auth.user.id, accountId)
      .then(data => commit(Types.TRANSACTIONS_FETCH_SUCCESS, data))
      .catch(err => commit(Types.TRANSACTIONS_FETCH_FAILURE, err))
  },
  [ Types.TRANSACTIONS_SET_RANGE ] ({commit}, name) {
    commit(Types.TRANSACTIONS_SET_RANGE, name)
  },
  [ Types.TRANSACTIONS_SET_CUSTOM_RANGE ] ({commit}, range) {
    commit(Types.TRANSACTIONS_SET_CUSTOM_RANGE, range)
  },
  [ Types.TRANSACTIONS_SET_TYPE ] ({commit}, type) {
    commit(Types.TRANSACTIONS_SET_TYPE, type)
  },
  [ Types.ACCOUNTS_GENERATE_STATEMENT ] ({commit, getters, state, rootState}, accountId) {
    commit(Types.ACCOUNTS_GENERATE_STATEMENT)
    const generateYearlyStatistics = state.range === 'fullYear' ? 'true' : 'false'
    const range = getters.getTransactionsRange
    TransactionsApi.generateStatement({
      clientId: rootState.auth.user.id,
      systemUserId: 1, // @todo ,   
      accountId,
      languageId: 'en', // @todo  .   
      startDate: range[0],
      endDate: range[1],
      debits: state.type === 'debits' || state.type === 'both' ? 'true' : 'false',
      credits: state.type === 'credits' || state.type === 'both' ? 'true' : 'false',
      generateYearlyStatistics
    })
  },

  [ Types.TRANSACTIONS_FETCH_ONE_REQUEST ] ({ commit, dispatch }, params) {
    commit(Types.TRANSACTIONS_FETCH_ONE_REQUEST)

    TransactionsApi.fetchOne(params)
      .then(data => commit(Types.TRANSACTIONS_FETCH_ONE_SUCCESS, data))
      .catch(err => commit(Types.TRANSACTIONS_FETCH_ONE_FAILURE, err))
  },

  [ Types.PENDING_TRANSACTIONS_FETCH_REQUEST ] ({ commit, dispatch }, params) {
    commit(Types.PENDING_TRANSACTIONS_FETCH_REQUEST)

    TransactionsApi.fetchPendingTransactions(params)
      .then(data => commit(Types.PENDING_TRANSACTIONS_FETCH_SUCCESS, data))
      .catch(err => commit(Types.PENDING_TRANSACTIONS_FETCH_FAILURE, err))
  },

  [Types.PENDING_TRANSACTIONS_UPDATE_PARAMS] ({commit}, pageNumber) {
    commit(Types.PENDING_TRANSACTIONS_UPDATE_PARAMS)
  }
}

const getters = {
  getTransactionsRange: state => {
    let from, to
    switch (state.range) {
      case '90days':
        from = Moment().subtract(90, 'days')
        to = Moment()
        break
      case 'currentMonth':
        let date = new Date()
        let y = date.getFullYear()
        let m = date.getMonth()
        let firstDay = new Date(y, m, 1)
        let lastDay = new Date(y, m + 1, 0)
        from = Moment(firstDay)
        to = Moment(lastDay)
        break
      case 'lastMonth':
        date = new Date()
        y = date.getFullYear()
        m = date.getMonth() - 1
        firstDay = new Date(y, m, 1)
        lastDay = new Date(y, m + 1, 0)
        from = Moment(firstDay)
        to = Moment(lastDay)
        break
      case 'today':
        to = from = Moment()
        break
      case 'yesterday':
        to = from = Moment().subtract(1, 'day')
        break
      case 'thisWeek':
        from = Moment().startOf('week')
        to = Moment().endOf('week')
        if (to >= Moment()) {
          to = Moment()
        }
        break
      case 'custom':
        from = state.customRange[0]
        to = state.customRange[1]
        break
      case 'fullYear':
        from = Moment().startOf('year')
        to = Moment()
    }

    from = from.format('YYYY-MM-DD')
    to = to.format('YYYY-MM-DD')
    return [from, to]
  },
  getTransactions: state => {
    return state.transactions
  },
  getTransactionStats: state => {
    if (!state.transactions.length) {
      return []
    }
    const dateToAmount = {}
    for (let v of state.transactions) {
      const date = Moment.utc(v.valueDate).format('YYYY-MM-DD')
      if (!dateToAmount[date]) dateToAmount[date] = [0, 0]
      if (v.amount >= 0) {
        dateToAmount[date][0] += v.amount
      } else {
        dateToAmount[date][1] += -v.amount
      }
    }
    let stats = Object.keys(dateToAmount).map(date => {
      return {
        timeRangeValueStart: date,
        data: {
          debitsAmount: dateToAmount[date][0],
          creditsAmount: dateToAmount[date][1]
        }
      }
    })
    stats = stats.sort((a, b) => {
      return new Date(a.timeRangeValueStart) - new Date(b.timeRangeValueStart)
    })
    if (!stats.length) {
      return stats
    }

    // fill by nulls from first day to now
    const to = Moment(stats[stats.length - 1].timeRangeValueStart)
    let from = Moment(stats[stats.length - 1].timeRangeValueStart).subtract(90, 'days')
    if (from > Moment(stats[0].timeRangeValueStart)) {
      from = Moment(stats[0].timeRangeValueStart)
    }
    const rangeDays = to.diff(from, 'days')
    let date
    const statsWithNulls = []

    // add nulls if it is so
    for (let addDays = 0; addDays <= rangeDays; addDays++) {
      date = Moment(from).add(addDays, 'days').format('YYYY-MM-DD')
      if (!dateToAmount[date]) {
        statsWithNulls.push({
          timeRangeValueStart: date,
          data: {
            debitsAmount: 0,
            creditsAmount: 0
          }
        })
      } else {
        statsWithNulls.push({
          timeRangeValueStart: date,
          data: {
            debitsAmount: dateToAmount[date][0],
            creditsAmount: dateToAmount[date][1]
          }
        })
      }
    }
    return statsWithNulls
  }
}

const mutations = {
  [ Types.TRANSACTIONS_FETCH_REQUEST ] (state) {
    state.loading = true
  },

  [ Types.TRANSACTIONS_FETCH_SUCCESS ] (state, data) {
    state.transactions = data
    state.error = null
    state.loading = false
  },

  [ Types.TRANSACTIONS_FETCH_FAILURE ] (state, err) {
    state.error = err
    state.loading = false
  },

  [ Types.TRANSACTIONS_SET_RANGE ] (state, name) {
    state.range = name
  },

  [ Types.TRANSACTIONS_SET_CUSTOM_RANGE ] (state, range) {
    state.customRange = range
  },

  [ Types.TRANSACTIONS_SET_TYPE ] (state, type) {
    state.type = type
  },

  [ Types.ACCOUNTS_GENERATE_STATEMENT ] (state) {
    state.statementInProgress = true
  },

  [ Types.TRANSACTIONS_FETCH_ONE_REQUEST ] (state) {
    state.loading = true
  },

  [ Types.TRANSACTIONS_FETCH_ONE_SUCCESS ] (state, data) {
    state.data = data
    state.error = null
    state.loading = false
  },

  [ Types.TRANSACTIONS_FETCH_ONE_FAILURE ] (state, err) {
    state.error = err
    state.loading = false
  },

  [ Types.PENDING_TRANSACTIONS_FETCH_REQUEST ] (state) {
    state.loading = true
  },

  [ Types.PENDING_TRANSACTIONS_FETCH_SUCCESS ] (state, {data, pageNumber, pageSize, totalItems, totalPages}) {
    state.data = data[0] ? data[0].transaction : null
    Object.assign(state.params, { pageNumber, pageSize, totalPages, totalItems })
    state.error = null
    state.loading = false
  },

  [ Types.PENDING_TRANSACTIONS_FETCH_FAILURE ] (state, err) {
    state.error = err
    state.loading = false
  },

  [ Types.PENDING_TRANSACTIONS_UPDATE_PARAMS ] (state, pageNumber) {
    state.params.pageNumber = pageNumber
  }
}

export default {
  state,
  actions,
  getters,
  mutations
}



