'use strict'

import Types from '@/store/types.js'
import ProductsApi from '@/api/products.js'

const state = {
  loading: false,
  error: null,
  products: {
    CURRENT: [],
    SAVINGS: [],
    FIXED_TERM: []
  }
}

const actions = {
  [ Types.PRODUCTS_FETCH_REQUEST ] ({commit, getters, rootState, dispatch}, type) {
    commit(Types.PRODUCTS_FETCH_REQUEST)
    ProductsApi.fetch(rootState.auth.user.id, type, 'USD')
      .then(data => {
        commit(Types.PRODUCTS_FETCH_SUCCESS, [data, type])

        if (!rootState.additional.models[type].productId && getters.getProducts(type).length) {
          dispatch(Types.ADDITIONAL_SET_MODEL_DATA, {
            type,
            data: {
              productId: getters.getProducts(type)[0].accountProductId
            }
          })
        }
      })
      .catch(err => commit(Types.PRODUCTS_FETCH_FAILURE, err))
  }
}

const getters = {
  getProducts: state => type => {
    return state.products[type]
  }
}

const mutations = {
  [ Types.PRODUCTS_FETCH_REQUEST ] (state) {
    state.loading = true
  },
  [ Types.PRODUCTS_FETCH_SUCCESS ] (state, params) {
    // console.log('>>>' + type)
    state.products[params[1]] = params[0]
    state.error = null
    state.loading = false
  },
  [ Types.PRODUCTS_FETCH_FAILURE ] (state, err) {
    state.error = err
    state.loading = false
  }
}

export default {
  state,
  actions,
  getters,
  mutations
}



