const files = require.context('./types', false, /\.js$/)

const scheme = files.keys().reduce((acc, k) => {
  Object.assign(acc, files(k).default.reduce((acc, ev) => {
    acc[ev] = ev
    return acc
  }, {}))

  return acc
}, {})

export default Object.freeze(scheme)



