<template>
  <div class="tabs-container">
    <ul class="tabs is-marginless">
      <li v-for="v in options"
        :key="v.id"
        :class="isSelected(v.id) ? 'selected' : null"
        class="text-body1"
        @click="select(v.id)">
        {{ v.title }}
      </li>
    </ul>
    <multiselect
      selectLabel=""
      selectedLabel=""
      deselectLabel=""
      :value="selectedOption"
      :options="options"
      :searchable="false"
      :allow-empty="false"
      track-by="id"
      label="title"
      @select="selectOption"
    >
    </multiselect>
  </div>
</template>

<script>
  export default {
    name: 'tabs',
    props: ['options', 'selected'],
    computed: {
      selectedOption () {
        for (let option of this.options) {
          if (option.id === this.selected) {
            return option
          }
        }
        return null
      }
    },
    methods: {
      isSelected (id) {
        return this.selectedOption.id === id
      },
      selectOption (option) {
        this.select(option.id)
      },
      select (id) {
        this.$emit('change', id)
      }
    }
  }
</script>

<style lang="scss">
  @import "../../../node_modules/bulma/sass/utilities/initial-variables";
  @import "../../styles/bulma/variables.scss";

  .tabs-container {
    .multiselect__tags {
      background: #fff;
      border-color: #fff;
    }
  }

  .tabs-container ul {
    border-top-right-radius: 6px;
    border-top-left-radius: 6px;
    background: $white-color;

    li.text-body1 {
      padding: 15px 0;
      font-weight: 400;
    }
  }

  .tabs-container .multiselect {
    display: none;
  }

  @media (max-width: $desktop) {
    .tabs-container .tabs {
      display: none;
    }
    .tabs-container .multiselect {
      display: block;
      margin-bottom: 22px;
    }
  }
</style>



