<template>
  <ul class="level">
    <router-link tag="li" to="/" class="text-body2">
      <svgicon name="language"></svgicon>
      EN
    </router-link>
    <router-link tag="li" to="/" class="text-body2">
      <svgicon name="support"></svgicon>
      Support
    </router-link>
    <router-link tag="li" to="/" class="text-body2">
      <svgicon name="profile"></svgicon>
      Profile
    </router-link>
    <router-link tag="li" to="/" class="logout text-body2">
      Logout
      <svgicon name="logout"></svgicon>
    </router-link>
  </ul>
</template>

<script>
  import '@/components/icons/language'
  import '@/components/icons/support'
  import '@/components/icons/profile'
  import '@/components/icons/logout'

  export default {
    name: 'top-nav'
  }
</script>

<style lang="scss" scoped>
  .level {
    li {
      padding: 0 12px;
      display: flex;
      align-items: center;
      font-weight: 400;

      .svg-icon {
        width: 24px;
        height: 24px;
        margin-right: 6px;
      }

      &:hover {
        cursor: pointer;
      }

      &.logout {
        border-left: 1px solid hsla(0,0%,100%,.3);
        padding-left: 24px;
        margin-left: 12px;

        .svg-icon {
          margin-left: 6px;
        }
      }

      &:last-child {
        padding-right: 0;
      }
    }
  }
</style>



