<template>
  <div>
    <div class="breadcrumbs text-caption">
      Demo Bank >
      <router-link to="/accounts">Accounts ></router-link>
      <a href="#">{{ account ? account.name : '...' }}</a>
    </div>
    <h1>Accounts</h1>
    <div v-if="loading">
      <div class="white-card body">
        <Preloader/>
      </div>
    </div>
    <div v-if="!loading">
      <div v-if="!account">
        <div class="white-card body">
          Account not found
        </div>
      </div>

      <div v-if="account">
        <Chart :account="account" />
        <div v-if="transactionsLoading" class="white-card body">
          <Preloader/>
        </div>
        <Transactions v-if="!transactionsLoading" :account="account" />
      </div>
    </div>
  </div>
</template>

<script>
  import Types from '@/store/types.js'
  import Chart from './Chart.vue'
  import Transactions from './transactions/List.vue'
  import Preloader from '@/components/common/Preloader'

  export default {
    name: 'account',
    components: {
      Chart,
      Transactions,
      Preloader
    },
    computed: {
      loading () {
        return this.$store.state.accounts.loading
      },
      transactionsLoading () {
        return this.$store.state.transactions.loading
      },
      id () {
        return parseInt(this.$route.params.id)
      },
      account () {
        const a = this.$store.getters.getAccounts().filter(v => {
          return v.id === this.id
        })
        return a[0] || null
      }
    },
    methods: {
      fetch () {
        if (!this.$store.state.accounts.length) {
          this.$store.dispatch(Types.ACCOUNTS_FETCH_REQUEST)
        }
        this.$store.dispatch(Types.TRANSACTIONS_FETCH_REQUEST, this.id)
      }
    },
    watch: {
      id: function () {
        this.$store.dispatch(Types.TRANSACTIONS_FETCH_REQUEST, this.id)
      }
    },
    beforeMount () {
      this.fetch()
    }
  }
</script>



