<template>
  <modal @close="close" ref="modal">
    <h3 slot="header">Choose date</h3>
    <div slot="body">
      <div class="input">
        <label>From:</label><br>
        <Datepicker
          class="date_picker"
          :on-change="onDateFromChange"
          :date="dateFrom"
        />
      </div>
      <div class="input">
        <label>To:</label><br>
        <Datepicker
          class="date_picker"
          :on-change="onDateToChange"
          :date="dateTo"
        />
      </div>
    </div>
    <div slot="footer">
      &nbsp;
      <button class="modal-default-button" @click="close">
        OK
      </button>
    </div>
  </modal>
</template>

<script>
  import Modal from '@/components/common/Modal.vue'
  import Datepicker from '@/components/common/Datepicker'

  export default {
    name: 'datepicker-modal',
    props: {
      onChange: Function,
      dateFrom: String,
      dateTo: String
    },
    data () {
      return {
        showModal: true
      }
    },
    components: {
      Modal,
      Datepicker
    },
    methods: {
      onDateFromChange (val) {
        this.dateFrom = val
        return this.onChange && this.onChange([this.dateFrom, this.dateTo])
      },
      onDateToChange (val) {
        this.dateTo = val
        return this.onChange && this.onChange([this.dateFrom, this.dateTo])
      },
      close () {
        this.$emit('close')
      }
    }
  }
</script>



