<template>
  <table class="table titled">
    <tbody>
    <tr>
      <td>Transfer amount</td>
      <td>{{ formatCurrency(model.amount, model.sourceAccount.currency) }}</td>
    </tr>
    <tr>
      <td>Source account IBAN</td>
      <td>{{ model.sourceAccount.iban }}</td>
    </tr>
    <tr>
      <td>Source account Name</td>
      <td>{{ model.sourceAccount.name }}</td>
    </tr>
    <tr>
      <td>Target account IBAN</td>
      <td>{{ model.targetAccount.iban }}</td>
    </tr>
    <tr>
      <td>Target account Name</td>
      <td>{{ model.targetAccount.name }}</td>
    </tr>
    </tbody>
  </table>
</template>

<script>
  import Currency from '@/components/mixin/currency'

  export default {
    name: 'transfers-summary',
    mixins: [Currency],
    computed: {
      model () {
        return this.$store.state.transfers.model
      }
    }
  }
</script>



