'use strict'

import Api from './api'
import Errors from './errors.json'
import { parseErrors } from '../utils.js'

export default {
  fetch (clientId) {
    return new Promise((resolve, reject) => {
      Api.get(`web-bank/accounts/clients/${clientId}`)
        .then(response => {
          if (response.data) {
            return resolve(response.data)
          }
          reject(new Error(Errors['NETWORK_ERROR']))
        })
        .catch(err => reject(parseErrors(err)))
    })
  },
  rename (clientId, accountId, name) {
    return new Promise((resolve, reject) => {
      Api.post(`web-bank/clients/${clientId}/accounts/${accountId}/rename`, {
        data: {
          name
        }
      })
      .then(response => {
        if (response.data) {
          return resolve(response.data)
        }
        reject(new Error(Errors['NETWORK_ERROR']))
      })
      .catch(err => reject(parseErrors(err)))
    })
  }
}



