<template>
  <StepsWrapper :step="step">
    <div slot="body">
      <div class="columns">
        <div class="column input full-width">
          <label for="account-name">Name your current account</label>
          <input
            type="text"
            v-model="name"
            name="name"
            id="account-name"
            placeholder="My current account"
            v-validate="'required'"
            :class="{'error': errors.has('name') }"
          />
        </div>
        <div class="column input">
          <label>Currency</label>
          <multiselect
            selectLabel=""
            selectedLabel=""
            deselectLabel=""
            v-model="productOption"
            placeholder="Currency"
            :allow-empty="false"
            :searchable="false"
            :options="productOptions"
            label="title"
            track-by="id"
          >
          </multiselect>
        </div>
        <div class="column input">
          <label>Applicable interest rate</label>
          <div class="h3">{{ rate }}%</div>
        </div>
      </div>

      <input
        type="checkbox"
        name="terms"
        id="terms"
        v-validate="'required'"
        :checked="model.agree"
        @change="onAgreeChanged"
      />

      <label for="terms" class="check">
        <i class="check"></i>
        I agree to the {{ typeTitle }} account <router-link to="/terms">Terms & Conditions</router-link>
      </label>

      <div class="help error" v-show="errors.has('terms')">{{ errors.first('terms') }}</div>

      <div class="subscript" v-if="type === 'SAVINGS'">
        <p><b>Special terms of savings accounts</b></p>
        <p>Transfers initiated by a client in relation to their savings account may only be performed between their Demo current account and their savings account. Clients may receive transfers into their savings account from other banks, however it is not possible to transfer money out of their Demo savings account to any other account but their Demo current account. The number of allowed transfers out of a clients savings account during a period may be capped.</p>

        <p>The applicable interest will be paid annually.</p>
      </div>

      <div class="button-footer">
        <button class="main-btn primary" @click="next" :disabled="!valid">Next
        </button>
      </div>
    </div>
  </StepsWrapper>
</template>

<script>
  import StepsWrapper from '../StepsWrapper.vue'
  import Step from '../step'
  import Step1 from '../step1'

  export default {
    name: 'additional-current-step1',
    mixins: [Step, Step1],
    components: {
      StepsWrapper
    },
    props: {
      type: {
        type: String,
        required: true
      }
    },
    computed: {
      productOption: {
        get () {
          if (!this.model.productId) {
            return this.productOptions[0]
          }
          return this.productOptions.find(c => c.id === this.model.productId)
        },
        set (v) {
          if (!v) {
            return
          }
          this.storeModelData({ productId: v.id })
        }
      },
      productOptions () {
        return this.$store.state.products.products[this.type].map(v => {
          return {
            id: v.accountProductId,
            title: v.currencyId
          }
        })
      },
      rate () {
        return this.$store.getters.getAdditionalRate(this.type)
      }
    }
  }
</script>

<style lang="scss" scoped>
  .subscript {
    margin-top: 50px;
  }
</style>



