<template>
  <StepsWrapper :step="step">
    <div slot="body">
      <div>
        <label>Select source account</label>
        <div class="card">
          <AccountSelectWide
            :account="model.sourceAccount"
            :on-change="onSourceAccountChange"
          />
        </div>
        <label>Select settlement account</label>
        <div class="card">
          <AccountSelectWide
            :account="model.settlementAccount"
            :on-change="onSettlementAccountChange"
          />
        </div>
      </div>
      <hr/>

      <Radios
        name="period"
        :options="productOptions"
        :value="productId + ''"
        :on-change="onProductChange"
      />
      <div class="columns total">
        <div class="column">
          <div>Deposit amount</div>
          <h2>{{ formatCurrency(depositAmount, currency) }}</h2>
        </div>
        <div class="column">
          <div>Interest earned</div>
          <h2>{{ formatCurrency(interestEarned, currency) }}</h2>
        </div>
      </div>
      <div class="columns">
        <div class="column input full-width">
          <label for="account-name">Name your current account</label>
          <input
            type="text"
            v-model="name"
            name="name"
            id="account-name"
            placeholder="My term account"
            v-validate="'required'"
            :class="{'error': errors.has('name') }"
          />
        </div>
        <div class="column">
          <Checkbox
            name="rollover"
            label="Rollover off"
            @change="onRolloverChange"
            :checked="rolloverOff"
          />
        </div>
      </div>

      <input
        type="checkbox"
        name="terms"
        id="terms"
        v-validate="'required'"
        :checked="model.agree"
        @change="onAgreeChanged"
      />
      <label for="terms" class="text-body2">
        <i class="check"></i>
        I agree to the fixed term Account
        <router-link to="/terms">Terms & Conditions</router-link>
      </label>

      <div class="help error" v-show="errors.has('terms')">{{ errors.first('terms') }}</div>

      <div class="button-footer">
        <button class="main-btn primary" @click="next" :disabled="!valid">Next
        </button>
      </div>
    </div>
  </StepsWrapper>
</template>

<script>
  import StepsWrapper from '../StepsWrapper.vue'
  import Step from '../step'
  import Step1 from '../step1'
  import AccountSelectWide from '@/components/common/AccountSelectWide.vue'
  import FetchAccounts from '@/components/mixin/fetchAccounts'
  import Radios from '@/components/common/Radios.vue'
  import Checkbox from '@/components/common/Checkbox.vue'
  import Common from './common'
  import Currency from '@/components/mixin/currency'

  export default {
    name: 'additional-term-step1',
    mixins: [Step, Step1, FetchAccounts, Common, Currency],
    components: {
      StepsWrapper,
      AccountSelectWide,
      Radios,
      Checkbox
    },
    computed: {
      type () {
        return 'FIXED_TERM'
      },
      rolloverOff () {
        return this.model.rolloverOff
      },
      productId () {
        if (this.model.productId) {
          return this.model.productId
        } else if (this.productOptions[0]) {
          return this.productOptions[0].value
        } else {
          return null
        }
      },
      productOptions () {
        return this.$store.state.products.products.FIXED_TERM.filter(v => v.currencyId === this.currency).map(v => {
          return {
            value: v.accountProductId,
            label: `<i>${this.formatMaturity(v.maturity)}</i> ${v.rate}% p.a.`
          }
        })
      },
      depositAmount () {
        return 50000
      },
      interestEarned () {
        const maturity = this.$store.getters.getAdditionalMaturity(this.type)
        const rate = this.$store.getters.getAdditionalRate(this.type)
        return this.depositAmount * (maturity / 12) * (rate / 100)
      }
    },
    methods: {
      onSourceAccountChange (id) {
        this.storeModelData({
          sourceAccount: this.$store.state.accounts.accounts.find(v => v.id === id)
        })
      },
      onSettlementAccountChange (id) {
        this.storeModelData({
          settlementAccount: this.$store.state.accounts.accounts.find(v => v.id === id)
        })
      },
      onProductChange (productId) {
        this.storeModelData({
          productId: parseInt(productId)
        })
      },
      onRolloverChange (rolloverOff) {
        this.storeModelData({
          rolloverOff
        })
      }
    }
  }
</script>

<style lang="scss" scoped>
  @import '../../../styles/colors.scss';

  .total {
    text-align: center;
    margin: 30px 0;

    h2 {
      font-size: 40px;
    }

    .column div {
      margin-bottom: 10px;
    }

    .column:last-child {
      border-left: 1px solid $light-gray;

      h2 {
        color: $green;
      }
    }
  }
</style>



