<template lang="html">
  <div class="paginator">
    <ul class="pages">
      <li 
        class="left"
        :class="{disable: currentPage === 1}"
        @click="onPageChange(currentPage - 1)"
        >
        <
      </li>
      <li
        v-for="page in list"
        :class="{ active: currentPage === page }"
        @click="onPageChange(page)"
      >
        {{ page }}
      </li>
      <li
        class="right"
        :class="{disable: currentPage === totalPages}"
        @click="onPageChange(currentPage + 1)"
        >
        >
      </li>
    </ul>
  </div>
</template>

<script>
export default {
  name: 'paginator',

  props: {
    params: Object,
    onPageChange: Function
  },

  computed: {
    totalPages () {
      return this.params ? this.params.totalPages : 1
    },

    currentPage () {
      return this.params ? this.params.pageNumber : 1
    },

    list () {
      const arr = []
      const page = this.currentPage
      const totalPages = this.totalPages

      let leftBoundry = Math.max(1, page - 1)
      let rightBoundry = Math.min(totalPages, page + 1)

      const emptyRight = 1 - (rightBoundry - page)
      const emptyLeft = 1 - (page - leftBoundry)

      leftBoundry = Math.max(1, leftBoundry - emptyRight)
      rightBoundry = Math.min(totalPages, rightBoundry + emptyLeft)

      for (let i = leftBoundry; i <= rightBoundry; i++) {
        arr.push(i)
      }

      return arr
    }
  }
}
</script>

<style lang="scss">
  @import "../../styles/colors.scss";

  .paginator {
    list-style-type: none;
    padding: 20px 0;
    display: flex;
    flex-wrap: nowrap;
    align-items: center;
    align-content: space-between;


    ul.pages {
      padding: 0;
      margin: 0;
      display: flex;

      li {
        box-sizing: border-box;
        border: 1px solid $white-color;
        color: $paginator-color-text;
        background: $paginator-background;
        min-width: 32px;
        min-height: 32px;
        line-height: 32px;
        text-align: center;
        font-size: 14px;
        vertical-align: middle;
        padding: 0;
        cursor: pointer;

        &.left,
        &.right {
          color: $paginator-arrow;
          background: $white-color;

          &.disable {
            color: $paginator-disable-arrow;
            cursor: default;
          }
        }

        &.left {
          border-top-left-radius: 3px;
          border-bottom-left-radius: 3px;
        }

        &.right {
          border-top-right-radius: 3px;
          border-bottom-right-radius: 3px;
        }


        // &:hover:not(.active) {
        //   border-color: $paginator-arrow;
        // }

        &.active {
          background: transparent;
          cursor: default;
        }
      }
    }
  }
</style>



