<template>
  <div class="radios">
    <template v-for="v in options">
      <Radio
        :name="name"
        :value="v.value"
        :label="v.label"
        :checked="v.value == value"
        v-on:input="onChange"
      />
    </template>
  </div>
</template>
<script>
  import Radio from './Radio.vue'

  export default {
    name: 'radios',
    components: {
      Radio
    },
    props: {
      value: {
        default: ''
      },
      name: {
        type: String,
        required: true
      },
      options: {
        type: Array,
        required: true
      },
      onChange: {
        type: Function,
        required: true
      }
    }
  }
</script>

<style lang="scss" scoped>
  .radios {
    display: flex;
    flex-wrap: wrap;
    align-content: space-between;

    .item {
      width: 25%;
      box-sizing: border-box;
      padding: 0 5px 5px 0;
    }

  }

  @media (max-width: 1024px) {
    .radios {
      .item {
        width: 50%;
      }
    }
  }

  @media (max-width: 768px) {
    .radios {
      .item {
        width: 100%;
      }
    }
  }

</style>



